<template>
    <div class="analysis">
        <Yheader title="数据分析"></Yheader>
        <div class="notice">
            <van-icon name="volume-o"/>
            <!--<span class="notice-title">现场测试发布公告超出会显示省略号</span>-->
            <van-swipe style="height: 0.85333rem;width: 100%" vertical loop :show-indicators="false">
                <van-swipe-item v-for="(item,index) in noticeList2" :key="index">{{item.title}}</van-swipe-item>
            </van-swipe>
        </div>

        <div class="analysis-echarts">
            <div class="analysis-echarts-title">兼职分布情况</div>
            <div class="echarts-bar" ref="echarts-bar"></div>
        </div>


        <div class="analysis-info">
            <div class="analysis-underway">
                <div class="analysis-underway-title">总的正在扫码的单子，接单地推归属省份分布</div>
                <div class="analysis-underway-content">
                    <span v-for="(item,index) in notAcceptTask" :key="index"><span v-if="index!=0">,</span>{{item.cityName}}（{{item.num}}）</span>
                </div>
            </div>
            <div class="analysis-underway">
                <div class="analysis-underway-title">总的未接单，省份分布</div>
                <div class="analysis-underway-content">
                    <span v-for="(item,index) in doingTask" :key="index"><span v-if="index!=0">,</span>{{item.cityName}}（{{item.num}}）</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {mapActions} from "vuex";
    import {getTaskStatistics} from "../../../api";

    export default {
        data() {
            return {
                noticeList2: [],
                doingTask: [],/*未接单*/
                notAcceptTask: [],/*扫码*/
                echartsObj: {
                    setOption: ""
                },


            }
        },
        components: {
            Yheader
        }
        ,
        async mounted() {
            this.noticeList().then(res => {
                if (res.code == 200) {
                    this.noticeList2 = res.data
                }
            }).catch(error => {
            })
            let echartsOption = {}
            let {code, data} = await getTaskStatistics()
            if (code == 200) {
                let xAxis = []
                let series = []
                let cityCode=[]
                data.partTimeJobsSituation.splice(0,5).forEach(function (item) {
                    item.cityCode = item.cityCode || 0
                    let str = item.rate.replace("%", "");
                    // str=parseInt(str);
                    xAxis.push(item.cityName || '')
                    series.push(str)
                    cityCode.push(item.cityCode)
                })
                this.notAcceptTask = data.notAcceptTask;
                this.doingTask = data.doingTask

                echartsOption = {
                    grid: {
                        top: '8%',
                        left: '8%',
                        right: '5%',
                        bottom: '20%',
                    },
                    tooltip: {
                        trigger: 'axis',
                        formatter: function (data) {
                            return cityCode[data[0].dataIndex]
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: xAxis,
                        axisLabel: {
                            inside: false,
                            interval: 'auto',
                            textStyle: {
                                color: '#333333'
                            }
                        },
                        axisTick: {
                            inside: true,
                            interval: 'auto'
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#2F7CFE'
                            }
                        }
                    },
                    yAxis: {
                        type: 'value',
                        max: 100,
                        nameTextStyle: {
                            color: 'red'
                        },
                        axisLabel: {
                            inside: false,
                            textStyle: {
                                color: '#ffffff'
                            },
                            formatter: '{value} %'
                        },
                        show: true,
                        axisTick: {
                            inside: true,//y轴刻度线
                            "show": false
                        },
                        splitLine: {     //网格线
                            "show": false,
                        },
                        // nameGap: 0,
                        axisLine: {
                            lineStyle: {
                                color: '#2F7CFE'
                            }
                        }


                    },
                    series: [{
                        data: series,
                        type: 'line',
                        areaStyle: {},

                        itemStyle: {
                            normal: {
                                color: '#9ABFFF',
                                label: {
                                    show: true, //开启显示
                                    position: 'bottom', //在上方显示
                                    textStyle: { //数值样式
                                        color: '#2F7CFE',
                                        fontSize: 15
                                    },
                                    formatter: function (a) {
                                        return a.data[0] + "%";
                                    },
                                }
                            }
                        },

                    }]

                }
            }
            console.log(echartsOption)
            this._initEcharts(echartsOption)
        }
        ,
        methods: {
            ...mapActions('user', ['noticeList']),
            _initEcharts(echartsOption) {
                this.echartsObj = this.$echarts.init(this.$refs['echarts-bar'])
                // console.log(echartsOption)
                this.echartsObj.setOption(echartsOption)

            }
        }
    }
</script>
<style lang="less" scoped>


    .analysis {
        min-height: 100vh;
        background: #F9F9F9;
        font-family: PingFangSC-Regular, PingFang SC;

        .notice {
            position: fixed;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: 32px;
            width: 345px;
            padding: 0 15px;
            background: rgba(255, 255, 255, 1);
            z-index: 11;
            .van-icon-volume-o {
                color: #ED5A1A;
                font-size: 15px;
            }

            .notice-title {
                margin-left: 7px;
                font-size: 12px;
                font-weight: 400;
                color: rgba(102, 102, 102, 1);
                line-height: 17px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            .van-swipe-item {
                line-height: 32px;
                padding-left: 7px;
            }
        }

        .analysis-echarts {
            margin: 44px 15px 0 15px;
            width: 321px;
            height: 248px;
            padding: 0 12px;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.04);
            border-radius: 8px;

            .analysis-echarts-title {
                width: 84px;
                height: 48px;
                font-size: 14px;
                font-weight: 500;
                color: rgba(47, 124, 254, 1);
                line-height: 48px;
            }

            .echarts-bar {
                width: 322px;
                height: 200px;
                background: #fff;

            }
        }

        .analysis-info {
            padding: 0 15px;

            .analysis-underway {
                width: 345px;
                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.04);
                border-radius: 8px;
                margin-top: 16px;

                .analysis-underway-title {
                    margin-left: 12px;
                    height: 48px;
                    font-size: 14px;
                    font-weight: 500;
                    color: rgba(47, 124, 254, 1);
                    line-height: 48px;
                }

                .analysis-underway-content {
                    padding: 0 12px 24px 12px;
                    height: 60px;
                    font-size: 12px;
                    font-weight: 400;
                    color: rgba(86, 149, 255, 1);
                    line-height: 20px;
                }
            }
        }

    }
</style>