const {body} = document;
export default {
    data() {
        return {
            aaa: ''
        }
    },
    created: function () {
        // console.log('方法混入')
    },
    mounted() {
    },
    methods: {
        vPhone(phone) {
            if (phone.length == 0) {
                this.$toast("请输入手机号码");
                return false;
            } else if (!(/^1[3456789]\d{9}$/.test(phone))) {
                this.$toast("手机号码有误，请重填");
                return false;
            }
        },
        xPhone(phone) {
            return phone.substr(0, 3) + '****' + phone.substr(7)
        },
        xDate(date) {
            return (/\d{4}-\d{1,2}-\d{1,2}/g.exec(date)[0])
        }

    },

    beforeDestroy: function () {


    },

}
