<template>
  <div class="publish">
    <Yheader title="任务发布"></Yheader>
    <div class="notice">
      <van-icon name="volume-o" />
      <!--<span class="notice-title">-->
      <!--<span v-for="(item,index) in noticeList2" :key="index">{{item.title}}</span>-->
      <!--</span>-->
      <van-swipe style="height: 0.85333rem;width: 100%" vertical loop :show-indicators="false">
        <van-swipe-item v-for="(item,index) in noticeList2" :key="index">{{item.title}}</van-swipe-item>
      </van-swipe>
    </div>

    <div class="nav-tab">
      <div
        v-for="(item,index) in navArray"
        :key="index"
        class="nav-tab-item"
        :class="[navActive==index?'nav-tab-active':'']"
        @click="onNav(index)"
      >
        <div class="nav-tab-item1" :class="[navActive==index?'nav-tab-item-active':'']">
          <span class="nav-tab-symbol">¥</span>
          <span class="nav-tab-price">{{item.price}}</span>
        </div>
        <div
          class="nav-tab-item2"
          :class="[navActive==index?'nav-tab-item-active':'']"
        >{{item.title}}</div>
        <div
          class="nav-tab-item3"
          :class="[navActive==index?'nav-tab-item3-active':'']"
        >{{item.type!=1?'设置':'充值'}}</div>
      </div>
    </div>
    <!--归属地-->
    <div class="home-location">
      <div class="home-location-title">归属地设置</div>
      <div class="home-location-content">
        <div
          class="hl-item"
          @click="phoneShow2=true,phoneType='phoneBelongsCityCode',areaList=userInfo.phoneBelongsCityCode"
        >
          <span>手机卡号归属地</span>
          <div class="hl-item-right" v-if="userInfo.phoneBelongsCityCode">
            <span v-for="(item,index) in provinces" :key="index">
              <span v-if="item.code==userInfo.phoneBelongsCityCode">{{item.name}}</span>
            </span>
            <van-icon name="arrow" />
          </div>
          <div class="hl-item-right" v-else>
            <span>非必选</span>
            <van-icon name="arrow" />
          </div>
        </div>
        <div
          class="hl-item"
          @click="phoneShow2=true,phoneType='equipmentIpBelongsCityCode',areaList=userInfo.equipmentIpBelongsCityCode"
        >
          <span>下单省份设置</span>
          <div class="hl-item-right" v-if="userInfo.equipmentIpBelongsCityCode">
            <span v-for="(item,index) in provinces" :key="index">
              <span v-if="item.code==userInfo.equipmentIpBelongsCityCode">{{item.name}}</span>
            </span>
            <van-icon name="arrow" />
          </div>
          <div class="hl-item-right" v-else>
            <span>必选</span>
            <van-icon name="arrow" />
          </div>
        </div>
        <div class="hl-item" @click="provinceShow=true">
          <span>接单省份设置</span>
          <div class="hl-item-right">
            <span></span>
            <van-icon name="arrow" />
          </div>
        </div>
        <div class="hl-item2">
          <span>备注</span>
          <textarea name id v-model="remark" placeholder="可输入手机号/字母，方便区分多个订单"></textarea>
        </div>
      </div>
    </div>
    <!--上传-->
    <input @change="fileChange($event)" type="file" id="upload_file" multiple v-show="false" />
    <div class="p-upload" @click="chooseType">
      <div class="p-upload-item">
        <img src="../assets/Ewm.png" alt />
        <span>扫码上传</span>
      </div>
      <!-- <div class="p-upload-item p-upload-item2" @click="$router.push({name:'phoneUp'})"> -->
      <div class="p-upload-item p-upload-item2" @click.stop="$toast('功能开发中')">
        <img src="../assets/upload.png" alt />
        <span>手机号上传</span>
      </div>
    </div>
    <!--发布指导-->
    <div class="guide">
      <div class="guide-title">发布指导</div>
      <div class="guide-item">
        <div class="guide-item-left">
          <div>发布价格</div>
          <div>实时指导</div>
        </div>
        <div class="guide-item-right">
          <div class="guide-ir-child">
            <div class="guide-irc-title">快速接单价格</div>
            <div class="guide-irc-price">
              <span>¥</span>
              {{fastAcceptOrderPrice}}
            </div>
            <div
              class="guide-irc-button"
              @click="getIssuedGuidance(), $toast.loading({
                        message: '刷新中...',
                        forbidClick: true
                    })"
            >刷新</div>
          </div>
        </div>
      </div>
      <div class="guide-item">
        <div class="guide-item-left guide-item-left2">
          <div>实时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
          <div>订单情况</div>
        </div>
        <div class="guide-item-right">
          <div class="guide-ir-child">
            <div class="guide-irc-title">平台待接单</div>
            <div class="guide-irc-price2">{{todoTaskCount}}</div>
            <div
              class="guide-irc-button"
              @click="getIssuedGuidance(), $toast.loading({
                        message: '刷新中...',
                        forbidClick: true
                    })"
            >刷新</div>
          </div>
        </div>
      </div>
    </div>

    <!--设置扫码发布价格-->
    <van-overlay :show="show" @click="show = false">
      <div class="wrapper" @click.stop>
        <div class="block">
          <div class="wrapper-title">{{wrapperForm.title}}</div>
          <div class="wrapper-tip">
            <span>提示：</span>输入价格最低{{minPrice/100}}元，最高{{maxPrice/100}}元
          </div>
          <div class="wrapper-input">
            <input type="number" v-model="wrapperForm.price" placeholder="输入手动发布价格" />
            <span>元</span>
          </div>
          <div class="wrapper-button">
            <div class="wrapper-close" @click="close">取消</div>
            <div class="wrapper-submit" @click="submint(navActive)">确认</div>
          </div>
        </div>
      </div>
    </van-overlay>
    <!--省份设置-->
    <van-popup v-model="provinceShow" position="bottom" :style="{ height: '55%' }">
      <div class="p-province">
        <div class="p-province-head">
          <span>接单省份设置</span>
          <div @click="provinceShow=false">关闭</div>
        </div>
        <div class="p-province-content">
          <div class="p-pc-item">
            <div class="p-province-p">是否定向省份</div>
            <van-radio-group
              v-model="directCity"
              direction="horizontal"
              checked-color="#FF6509"
              icon-size="16px"
              @change="changeCity"
            >
              <van-radio name="1" style="margin-right: 40px" >定向</van-radio>
              <van-radio name="0" >非定向</van-radio>
            </van-radio-group>
          </div>
          <div class="p-pc-item">
            <div class="p-province-p">设置接单省份</div>
            <div class="noAssgin" v-if="directCity==0">非定向</div>
            <div class="noAssgin2" v-else @click.stop="provinceShow2=true">请选择</div>
            <div class="p-Assgin" v-if="directCity==1">
              <van-icon name="arrow-down" />
            </div>
          </div>
          <div class="p-province-sel" v-if="directCity==1">
            <span v-for="(item,index) in provinces" :key="index">
              <span class="select-item" @click="onProvinces(item.code)" v-if="directCityArray.split(',').indexOf(item.code)>-1">
                {{item.name}}
                <van-icon name="cross" />
                <!-- <span v-if="item.code!=directCityArray.split(',')[0]">|</span> -->
              </span>
            </span>
          </div>
        </div>
        <div class="p-province-tip">
          <span class="p-province-tip-color">【1】</span>
          <span>选择“定向”出了所选省份你的兼职能接到，其余兼职无权限接到您的订单：</span>
          <span class="p-province-tip-color">注意：请慎重选择该选项，因定向订单接单人数少，如非必选请勿选！</span>
          <br />
          <span class="p-province-tip-color">【2】</span>
          <span>“全部”，“未知省份”“不填资料（港澳台，国外），请选非定向</span>
        </div>
      </div>
      <!--选择省份-->
      <van-popup v-model="provinceShow2" position="bottom" :style="{ height: '40%' }">
        <div class="p-provinces-f" @click="provinceShow2=false">关闭</div>
        <div class="p-provinces">
          <div
            class="p-provinces-list"
            v-for="(item,index) in provinces"
            :class="[directCityArray.split(',').indexOf(item.code)>-1?'p-provinces-itemActive':'']"
            :key="index"
            @click="onProvinces(item.code)"
          >
            {{item.name}}
            <van-icon name="success" />
          </div>
        </div>
      </van-popup>
    </van-popup>

    <!--归属手机号设置-->
    <van-popup v-model="phoneShow2" position="bottom" :style="{ height: '40%' }">
      <div class="p-provinces-f" @click="phoneShow2=false">关闭</div>
      <div class="p-provinces">
        <div
          class="p-provinces-list"
          v-for="(item,index) in provinces"
          :class="[item.code==areaList?'p-provinces-itemActive':'']"
          :key="index"
          @click="onProvinces2(item.code)"
        >{{item.name}}</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import Yheader from "../Yheader/index";
// import AreaList from '../area';
import { codePublish,getConfig } from "../../../api/login";
import { uploadFile } from "../../../api/login";
import { mapActions, mapState } from "vuex";
import { getIssuedGuidance } from "../../../api";
import { Toast, RadioGroup, Radio} from 'vant'

export default {
  data() {
    return {
      remark:'',
      navActive: 0,
      navArray: [
        { price: 100.1, title: "当前余额", type: "1" },
        { price: 12.8, title: "扫码发布价格", type: "2" },
        { price: 6.8, title: "api发布价格", type: "3" },
        { price: 6.8, title: "手机发布价格", type: "4" }
      ],
      message: "",
      show: false,
      wrapperForm: {
        price: "",
        title: ""
      },
      /*设置省份*/
      provinceShow: false,
      provinceShow2: false,
      directCity: "1",
      provinces: [],
      // directCityArray: [],
      noticeList2: [],

      todoTaskCount: "",
      fastAcceptOrderPrice: "",
      phoneShow2: false,
      areaList: "",
      phoneType: "phoneBelongsCityCode",
      directCityArray123: "",
      intervalObj: {
        minCodePublishPrice: 0,
        maxCodePublishPrice: 0,
        minPhonePublishPrice: 0,
        maxPhonePublishPrice: 0,
        minApiPublishPrice: 0,
        maxApiPublishPrice: 0
      },
      minPrice: 0,
      maxPrice: 0,
    };
  },
  components: {
    Yheader
  },
  computed: {
    
    ...mapState("user", {
      userInfo: state => state.userInfo,
      directCityArray: state => state.userInfo.directCityArray || "",
      directCitys: state => state.directCity
    }),
    ...mapState("city", {
      province: state => state.province
    })
  },
  watch: {
    directCity: function(newVal) {
      this.set_directCity(newVal);
    }
  },
  created() {

    getConfig().then(res => {
      this.intervalObj = res.data
    })
  },
  async mounted() {
    this.$nextTick(function() {
      this.directCity = this.directCitys.toString();
      this.navArray[0].price = this.userInfo.balance;
      this.navArray[1].price = this.userInfo.codePrice;
      this.navArray[2].price = this.userInfo.apiPrice;
      this.navArray[3].price = this.userInfo.phonePrice;
      // this.directCity = this.userInfo.directCity ?this.userInfo.directCity.toString():'1'
      if (this.province.length == 0) {
        this.get_province()
          .then(response => {
            const { code, data } = response;
            if (code == 200) {
              this.provinces = data;
            }
          })
          .catch(() => {});
      } else {
        this.provinces = this.province;
      }
    });
    this.noticeList()
      .then(res => {
        if (res.code == 200) {
          this.noticeList2 = res.data;
        }
      })
      .catch(error => {
        console.log(error);
      });
    this.getIssuedGuidance();
    this.userinfos()
    console.log(this.directCity)
    // this.QRcode()
  },
  methods: {
    fileChange(event) {
      let that = this;
      let files = event.target.files[0];
      uploadFile(files).then(res => {
        if (res.code == 200) {
          that.QRcode(res.data[0].fileUrl);
        }
      });
      // var reader = new FileReader();
      // reader.readAsDataURL(files);
      // reader.onload = function() {
      //   console.log(reader.result)
      //   that.QRcode(reader.result);
      // };
    },
    chooseType() {
      let that = this;
      // // console.log('1123123')
      // // document.getElementById("upload_file").click();

      // var permissions = cordova.plugins.permissions;
      // //校验app是否有安卓写入权限
      // permissions.checkPermission(permissions.CAMERA, function(s) {
      //   //hasPermission 验证是否成功
      //   // alert('hasPermission---'+s.hasPermission)
      //   if (!s.hasPermission) {
      //     //没有权限
      //     //app申请写入权限
      //     permissions.requestPermission(
      //       permissions.CAMERA,
      //       function(s) {
      //         if (s.hasPermission) {
      //           //申请成功
      //           scanner();
      //         } else {
      //           //申请失败
      //         }
      //       },
      //       function(error) {}
      //     );
      //   } else {
      //     //拥有权限
      //     scanner();
      //   }
      // });

      // function scanner() {
      try {
        let remark = this.remark;
        cordova.plugins.barcodeScanner.scan(
          function(result) {
            let obj = result.text;
            // //扫码成功后执行的回调函数
            codePublish({ qrCodeUrl: obj ,remark: remark}).then(res => {
              // if(res.code == 200) {
              //   that.$toast('上传成功')
              // } else {
              //   that.$toast(res.msg);
              // }
              Toast(res.msg);
            });
          },
          function(error) {
            //扫码失败执行的回调函数
          },
          {
            preferFrontCamera: false, // iOS and Android 设置前置摄像头
            torchOn: false, // Android, launch with the torch switched on (if available)打开手电筒
            prompt: "在扫描区域内放置二维码", // Android提示语
            resultDisplayDuration: 10, // Android, display scanned text for X ms.
            //0 suppresses it entirely, default 1500 设置扫码时间的参数
            formats: "QR_CODE", // 二维码格式可设置多种类型
            orientation: "portrait", // Android only (portrait|landscape),
            //default unset so it rotates with the device在安卓上 landscape 是横屏状态
            disableAnimations: true, // iOS     是否禁止动画
            disableSuccessBeep: false // iOS      禁止成功后提示声音 “滴”
          }
        );
      } catch (e) {
        //H5无法调用摄像头执行上传图片
        document.getElementById("upload_file").click();
        // that.$toast(e);
      }
      // }
    },
    ...mapActions("user", [
      "set_directCity",
      "noticeList",
      "userinfos",
      "updateInfo2"
    ]),
    ...mapActions("city", ["get_province"]),
    async getIssuedGuidance() {
      let { code, data } = await getIssuedGuidance();
      if (code == 200) {
        this.todoTaskCount = data.todoTaskCount / 100;
        this.fastAcceptOrderPrice = data.fastAcceptOrderPrice / 100;
      }
    },
    /*导航点击*/
    onNav(val) {
      this.navActive = val;
      Object.assign(this.wrapperForm, this.navArray[val]);
      if (val == 0) {
        this.$toast("充值走线下，请联系客服");
      } else if (val == 1) {
        this.show = true;
        this.minPrice = this.intervalObj.minCodePublishPrice
        this.maxPrice = this.intervalObj.maxCodePublishPrice
      } else if (val == 2) {
        this.show = true;
        this.minPrice = this.intervalObj.minApiPublishPrice
        this.maxPrice = this.intervalObj.maxApiPublishPrice
      } else if (val == 3) {
        this.show = true;
        this.minPrice = this.intervalObj.minPhonePublishPrice
        this.maxPrice = this.intervalObj.maxPhonePublishPrice
      }
    },
    async QRcode(img) {
      console.log(img);
      let remark = this.remark
      let { msg } = await codePublish({
        qrCodeUrl: img,
        remark:remark
      });
      this.$toast(msg);
    },
    submint(index) {
      let type = this.navArray[index].type;
      let obj = {};

      if (this.wrapperForm.price < 6.6 || this.wrapperForm.price > 30) {
        this.$toast("输入价格最低6.6元，最高30元");
        return false;
      }
      if (type == 2) {
        obj["codePrice"] = this.wrapperForm.price * 100;
      } else if (type == 3) {
        obj["apiPrice"] = this.wrapperForm.price * 100;
      } else if (type == 4) {
        obj["phonePrice"] = this.wrapperForm.price * 100;
      }

      this.updateInfo2(obj)
        .then(response => {
          this.defend = false;
          const { code, msg } = response;
          if (code == 200) {
            this.$toast("设置成功");
            this.navArray[index].price = this.wrapperForm.price;
          } else {
            this.$toast(msg);
          }
          this.close();
        })
        .catch(() => {
          this.defend = false;
        });
    },
    close() {
      this.show = false;
      this.wrapperForm = this.$options.data().wrapperForm;
    },
    changeCity() {
      if (this.directCity == 0) {
        this.updateInfo2({ directCity: this.directCity })
          .then(response => {
            this.defend = false;
            const { code, msg } = response;
            if (code == 200) {
            } else {
              // this.directCity = 1;
              this.$toast(msg);
            }
            this.close();
          })
          .catch(() => {
            this.defend = false;
          });
      } else if(this.directCity == 1 && this.directCityArray.length > 0) {
        this.updateInfo2({
          directCity: this.directCity,
          directCityArray: this.directCityArray.split(",")
        })
          .then(response => {
            this.defend = false;
            const { code, msg } = response;
            if (code == 200) {
            } else {
              // this.directCity = 0;
              this.$toast(msg);
            }
            this.close();
          })
          .catch(() => {
            this.defend = false;
          });
      }
    },
    onProvinces(val) {
      let directCityArray2 = this.directCityArray.split(",");
      if (directCityArray2.indexOf(val) > -1) {
        directCityArray2.splice(directCityArray2.indexOf(val), 1);
      } else {
        directCityArray2 = `${this.directCityArray},${val}`.split(",");
      }
      this.updateInfo2({
        directCity: this.directCity,
        directCityArray: directCityArray2
      })
        .then(response => {
          this.defend = false;
          const { code, msg } = response;
          if (code != 200) {
            this.$toast(msg);
          }
          this.close();
        })
        .catch(() => {
          this.defend = false;
        });
    },
    onProvinces2(val) {
      let obj = {};
      obj[this.phoneType] = val;
      this.updateInfo2(obj)
        .then(response => {
          const { msg } = response;
          this.phoneShow2 = false;
          this.$toast(msg);
          this.close();
        })
        .catch(() => {});
    }
  }
};
</script>
<style lang="less" scoped>
.publish {
  min-height: 100vh;
  background: #f9f9f9;
  font-family: PingFangSC-Regular, PingFang SC;
  overflow: auto;
  .notice {
    position: fixed;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 32px;
    width: 345px;
    padding: 0 15px;
    background: rgba(255, 255, 255, 1);

    .van-icon-volume-o {
      color: #ed5a1a;
      font-size: 15px;
    }

    .notice-title {
      margin-left: 7px;
      font-size: 12px;
      font-weight: 400;
      color: rgba(102, 102, 102, 1);
      line-height: 17px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .van-swipe-item {
      line-height: 32px;
      padding-left: 7px;
    }
  }

  /*发布导航*/

  .nav-tab {
    padding: 0 12px;
    margin-top: 42px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    .nav-tab-item {
      width: 80px;
      height: 110px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.06);
      border-radius: 8px;
      margin: 0 4px;
      display: flex;
      flex-flow: column;
      align-items: center;

      .nav-tab-item1 {
        margin-top: 20px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(255, 83, 9, 1);

        .nav-tab-symbol {
          font-size: 10px;
        }
      }

      .nav-tab-item2 {
        margin-top: 6px;
        font-size: 11px;
      }

      .nav-tab-item3 {
        margin-top: 14px;
        width: 42px;
        height: 18px;
        text-align: center;
        line-height: 18px;
        color: #fff;
        font-size: 11px;
        background: rgba(255, 83, 9, 1);
        border-radius: 3px;
      }

      .nav-tab-item-active {
        color: white;
      }

      .nav-tab-item3-active {
        color: #ff5c09;
        background: #fff;
      }
    }

    .nav-tab-active {
      background: linear-gradient(
        189deg,
        rgba(255, 65, 9, 1) 0%,
        rgba(255, 101, 9, 1) 100%
      );
      box-shadow: 0px 2px 10px 0px rgba(247, 205, 188, 1);
      border-radius: 8px;
    }
  }

  /*归属地*/

  .home-location {
    padding: 0 15px;

    .home-location-title {
      height: 48px;
      font-size: 16px;
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      line-height: 48px;
    }

    .home-location-content {
      width: 345px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.06);
      border-radius: 8px;

      .hl-item {
        padding: 0 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
