<template>
    <div class="transferDetail">
        <Yheader title="转移明细" :backBoole="true" :shadow="false" @_goBack="$router.go(-1)"></Yheader>
        <div class="transferDetail-head-demo"></div>
        <div class="transferDetail-head">
            <div :class="[!detailActive?'transferDetail-head-active':'']" @click="detailActive=false">转移支出明细</div>
            <div :class="[detailActive?'transferDetail-head-active':'']" @click="detailActive=true">转移收入明细</div>
        </div>
        <!--<van-pull-refresh v-model="refreshing" @refresh="onRefresh" class="transferDetail-content">-->
        <!--<van-list-->
        <!--:immediate-check="true"-->
        <!--v-model="loading"-->
        <!--:finished="finished"-->
        <!--finished-text="没有更多了"-->
        <!--@load="onLoad"-->
        <!--&gt;-->
        <span v-if="!detailActive">
         <div v-for="(item,index) in transferPayOffList" :key="index" class="transferDetail-content-item">
             <div class="tc-item-date">{{item.time}}</div>
             <div class="tc-item">
                 <div class="tc-item-user">向账户【{{xPhone(item.phone)}}】</div>
                 <span class="tc-item-span">
                     <div class="transfer-price">转移账款</div>
                     <div class="tc-item-pirce">-{{item.amount.toFixed(2)}}元</div>
                 </span>
             </div>
         </div>
        </span>
        <span v-if="detailActive">
                    <div v-for="(item,index) in transferIncomeList" :key="index" class="transferDetail-content-item">
                        <div class="tc-item-date">{{item.time}}</div>
                        <div class="tc-item">
                            <div class="tc-item-user">向账户【{{xPhone(item.phone)}}】</div>
                            <span class="tc-item-span">
                                <div class="transfer-price">转移账款</div>
                                <div class="tc-item-pirce tc-item-pirce2">+{{item.amount.toFixed(2)}}元</div>
                            </span>
                        </div>
                    </div>
                </span>
        <!--</van-list>-->
        <!--</van-pull-refresh>-->


    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {queryTransferPayOffList,queryTransferIncomeList} from "../../../api/mine";
    import mixin from '@/mixin'
    export default {
        data() {
            return {
                detailActive: false,
                list: [],
                loading: false,
                finished: false,
                refreshing: false,
                transferIncomeList:[],/*收入*/
                transferPayOffList: [],
            }
        },
        components: {
            Yheader
        },
        mixins: [mixin],
        watch:{
            'detailActive':function (newVal,oldVal) {
                if(newVal){
                   this.queryTransferPayOffList()
                }else {
                    this.queryTransferIncomeList()
                }
            }
        },

        mounted() {
            this.queryTransferIncomeList()
        },
        methods: {
            async queryTransferIncomeList() {
                let that=this
                let {code, data} = await queryTransferIncomeList();
                if (code == 200) {
                    data.forEach(function (item) {
                        item.amount=item.amount/100
                        that.transferIncomeList.push(item)
                    })

                }
            },
            async queryTransferPayOffList() {
                let that=this
                let {code, data} = await queryTransferPayOffList();
                if (code == 200) {
                    data.forEach(function (item) {
                        item.amount=item.amount/100
                        that.transferPayOffList.push(item)
                    })
                }
            },
            onLoad() {
                setTimeout(() => {
                    if (this.refreshing) {
                        this.list = [];
                        this.refreshing = false;
                    }

                    for (let i = 0; i < 10; i++) {
                        this.list.push(this.list.length + 1);
                    }
                    this.loading = false;

                    if (this.list.length >= 40) {
                        this.finished = true;
                    }
                }, 1000);
            },
            onRefresh() {
                // 清空列表数据
                this.finished = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loading = true;
                this.onLoad();
            },

        }
    }
</script>
<style lang="less" scoped>
    .transferDetail {
        min-height: 100vh;
        background: #F9F9F9;
        font-family: PingFangSC-Regular, PingFang SC;

        .transferDetail-head-demo {
            height: 44px;
        }

        .transferDetail-head {
            position: fixed;
            top: 44px;
            left: 0;
            width: 375px;
            height: 44px;
            background: rgba(255, 255, 255, 1);
            display: flex;
            justify-content: space-between;
            align-items: center;
            z-index: 1;

            div {
                width: 188px;
                height: 44px;
                font-size: 16px;
                font-weight: 400;
                color: rgba(102, 102, 102, 1);
                line-height: 44px;
                text-align: center;
            }

            .transferDetail-head-active {
                font-weight: 500;
                color: rgba(51, 51, 51, 1);
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    margin-left: -24px;
                    width: 48px;
                    height: 3px;
                    background: #FF6509;
                }
            }


        }

        .transferDetail-content {
            height: calc(100vh - 88px);
            overflow-y: scroll;
            padding: 14px 15px;

            .transferDetail-content-item {
                width: 345px;
                margin-bottom: 10px;

                .tc-item-date {
                    text-align: center;
                    height: 33px;
                    font-size: 12px;
                    font-weight: 400;
                    color: rgba(102, 102, 102, 1);
                    line-height: 33px;
                }

                .tc-item {
                    width: 345px;
                    height: 48px;
                    box-shadow: 0px 1px 8px 0px rgba(246, 246, 246, 1);
                    border-radius: 8px;
                    background: white;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .tc-item-user {
                        width: 160px;
                        height: 48px;
                        font-size: 14px;
                        font-weight: 400;
                        color: rgba(51, 51, 51, 1);
                        line-height: 48px;
                    }

                    .tc-item-span {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;

                        .transfer-price {
                            width: 56px;
                            height: 48px;
                            font-size: 14px;
                            font-weight: 400;
                            color: rgba(51, 51, 51, 1);
                            line-height: 48px;
                        }

                        .tc-item-pirce {
                            min-width: 60px;
                            height: 48px;
                            font-size: 14px;
                            font-weight: 500;
                            color: rgba(255, 40, 9, 1);
                            line-height: 48px;
                            margin-right: 16px;
                            margin-left: 12px;
                            text-align: right;
                        }

                        .tc-item-pirce2 {
                            color: #00C929;
                        }
                    }

                }
            }
        }
    }
</style>