<template>
    <div class="phoneUp">
        <Yheader title="手机号上传" :backBoole="true" :shadow="false" @_goBack="$router.go(-1)">
            <template slot="right">
                <div class="phone-h-right" @click="refresh">
                    <van-icon name="replay"/>
                    <span>刷新</span>
                </div>
            </template>
        </Yheader>
        <div class="phone-demo"></div>
        <div class="phone-search">
            <div class="phone-search-input">
                <input type="number" v-model="phone" placeholder="输入需要辅助的手机号">
                <img src="../assets/phone.png" alt="">
            </div>
            <div class="phone-search-add" @click="searchAdd">添加</div>
            <div class="phone-search-Aadd" @click="show=true">批量添加</div>
        </div>
        <div class="phone-list">
            <!-- <van-list
                     v-model="loading"
                     :finished="finished"
                     finished-text="没有更多了"
                     @load="onLoad"
             >-->
            <van-checkbox-group v-model="phoneResult" checked-color="#FF6509" icon-size="16px">
                <van-checkbox class="phone-list-item" :name="index" v-for="(item,index) in list" :key="index"
                               :disabled="item.status!='0'"
                >
                    <!--:disabled="index==1"-->
                    <div class="phone-list-child">
                        <div class="phone-lc-title">
                            <div class="phone-lc-tpl">【{{index+1}}】{{item.phone}}</div>
                            <div class="phone-lc-tip tip-color1" v-if="item.status=='0'">正在传输</div>
                            <div class="phone-lc-tip tip-color2" v-if="item.status=='3'">{{item.statusName}}</div>
                            <div class="phone-lc-tip tip-color3" v-if="item.status=='2'">{{item.statusName}}</div>
                            <div class="phone-lc-tip tip-color4" v-if="item.status=='1'">{{item.statusName}}</div>
                        </div>
                        <div class="phone-lc-date">执行辅助时间：{{item.createTime}}</div>
                        <div class="phone-lc-date">注册有效期：</div>
                        <div class="phone-lc-button">
                            <div class="phone-lc-submit" @click="addPhone(item.phone)" v-if="item.status=='0' ">执行辅助</div>
                            <div @click.stop="delPhone2(item.phone)">删除数据</div>
                        </div>
                    </div>
                </van-checkbox>
            </van-checkbox-group>
            <!--</van-list>-->
        </div>
        <div class="phone-demo"></div>
        <div class="phone-footer">
            <div class="phone-footer-del" @click="delPhone(phoneResult)">批量删除</div>
            <div class="phone-footer-execute" @click="addPhone('')">批量执行</div>
        </div>

        <!--批量添加手机号-->
        <van-overlay :show="show" @click="show = false">
            <div class="wrapper" @click.stop>
                <div class="block">
                    <div class="wrapper-title">批量添加手机号</div>
                    <div class="wrapper-content-h">注意：多个手机号使用逗号分隔，只能输入数字和逗号</div>
                    <div class="wrapper-content">
                        <textarea v-model="phoneList" placeholder="请输入手机号"
                                  onkeyup="value=value.replace(/[^\d|,]/g,'')"></textarea>
                    </div>
                    <div class="wrapper-button">
                        <div class="wrapper-close" @click="close">取消</div>
                        <div class="wrapper-submit" @click="submint(),close()">批量添加</div>
                    </div>
                </div>
            </div>
        </van-overlay>
    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {queryPhoneOrderList, phoneDelete, phonePublish, phoneSuccess, phoneFail} from "@/api/index";
    import {mapState} from "vuex";


    export default {
        data() {
            return {
                phone: '',
                show: false,
                list: [],
                loading: false,
                finished: false,
                phoneResult: [],
                phoneList: [],/*批量上传*/
            }
        },
        components: {
            Yheader
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
                directCity: state => state.directCity,
            }),
            ...mapState('city', {
                province: state => state.province,
            })
        },
        mounted() {
            this.queryPhoneOrderList()
        },
        methods: {
            async queryPhoneOrderList() {
                let {code, data} = await queryPhoneOrderList()
                if (code == 200) {
                    this.list = data
                }
            },
            refresh() {
                this.$toast.loading({
                    message: '刷新中...',
                    forbidClick: true
                });
                this.queryPhoneOrderList()
            },
            searchAdd() {
                if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
                    this.$toast('手机号码有误,请重填');
                    return false;
                }
                this.phoneList = this.phone
                this.submint()
            },
            async submint() {
                let that = this
                let phoneList = []
                this.phoneList.split(',').forEach(function (item) {
                    phoneList.push({
                        phone: item,
                        createTime: that.formatDateTime(new Date()),
                        statusName: "传输中",
                        id: null,
                        endTime: null,
                        status: 0
                    })
                })
                this.list = [...phoneList, ...this.list]
                // this.addPhone();
            },
            formatDateTime(date) {
                let y = date.getFullYear();
                let m = date.getMonth() + 1;
                m = m < 10 ? ('0' + m) : m;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                let h = date.getHours();
                h = h < 10 ? ('0' + h) : h;
                let minute = date.getMinutes();
                minute = minute < 10 ? ('0' + minute) : minute;
                let second = date.getSeconds();
                second = second < 10 ? ('0' + second) : second;
                return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
            },
            /*批量删除*/
            async delPhone2(val) {
                this.deleteP([val])
            },
            delPhone() {
                let that = this
                let phoneList = []
                that.phoneResult.forEach(function (item) {
                    phoneList.push(that.list[item].phone)
                })
                this.deleteP(phoneList)


            },
            deleteP(phoneList) {
                let that = this
                this.$dialog.confirm({
                    title: '提示',
                    message: '确定删除'
                }).then(async () => {

                    let {code} = await phoneDelete(phoneList)
                    if (code == 200) {
                        that.$toast.loading({
                            message: '删除中...',
                            forbidClick: true
                        });
                        that.queryPhoneOrderList()
                    }
                }).catch(() => {
                    // on cancel
                });

            },
            /*批量,执行*/
            async addPhone(phone) {
                let that = this;
                 var phoneList = [];
                if(phone){
                    phoneList.push(phone);
                }else{
                    this.phoneResult.forEach(function (item) {
                        phoneList.push(that.list[item].phone)
                    })
                }
                let obj  = {
                    'phoneList': phoneList
                };
                let {code, msg} = await phonePublish(obj)
                if (code == 200) {
                    this.queryPhoneOrderList()
                    this.$toast(msg)
                }else{
                     this.$toast(msg)
                }
            },
            
            phoneSuccess(val) {
               
                this.$dialog.confirm({
                    title: '确认任务',
                    message: '是否成功',
                    confirmButtonText: '成功',
                    cancelButtonText: '失败'
                }).then(async () => {
                    // on confirm
                    let {code,msg} = await phoneSuccess({orderId: val.id})
                    if(code==200){
                        this.queryPhoneOrderList()
                        this.$toast(msg)
                    }
                }).catch(async () => {
                    let {code,msg} = await phoneFail({orderId: val.id})
                    if(code==200){
                        this.queryPhoneOrderList()
                        this.$toast(msg)
                    }
                });
            },
            close() {
                this.show = false;
                // this.wrapperForm = this.$options.data().wrapperForm
            },
            onLoad() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < 10; i++) {
                        this.list.push(this.list.length + 1);
                    }

                    // 加载状态结束
                    this.loading = false;

                    // 数据全部加载完成
                    if (this.list.length >= 40) {
                        this.finished = true;
                    }
                }, 1000);
            }
        }
    }
</script>
<style lang="less">
    .phoneUp {
        .phone-list {
            .van-checkbox__icon {
                padding-top: 8px;
            }
        }
    }
</style>
