        }

        /*弹窗*/

        .wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;

            .block {
                width: 322px;
                background: rgba(255, 255, 255, 1);
                border-radius: 8px;

                .wrapper-title {
                    text-align: center;
                    height: 48px;
                    font-size: 16px;
                    font-weight: 500;
                    color: rgba(53, 53, 53, 1);
                    line-height: 48px;
                }

                .wrapper-content-h {
                    margin: 0 16px;
                    width: 290px;
                    height: 32px;
                    background: rgba(255, 248, 244, 1);
                    border-radius: 6px 6px 0px 0px;
                    border: 1px solid rgba(255, 101, 9, 1);
                    border-bottom: none;
                    font-size: 11px;
                    text-align: center;
                    font-weight: 400;
                    color: rgba(255, 101, 9, 1);
                    line-height: 32px;
                }

                .wrapper-content {
                    margin: 0 16px;
                    width: 290px;
                    height: 188px;
                    background: rgba(255, 255, 255, 1);
                    border-radius: 0px 0px 6px 6px;
                    border: 1px solid rgba(255, 101, 9, 1);
                    text-align: center;

                    textarea {
                        border: none;
                        width: 260px;
                        height: 160px;
                        resize: none;
                        padding: 9px 10px;

                        &::-webkit-input-placeholder {
                            /* placeholder颜色  */
