<template>
    <div class="transferPwd">
        <Yheader title="设置转移密码" :backBoole="true" :shadow="false" @_goBack="$router.go(-1)"></Yheader>


        <div class="transferPwd-content">
            <div class="transferPwd-item">
                <div class="transferPwd-item-label">手机号</div>
                <div class="transferPwd-item-tpl">{{xPhone(userInfo.phone)}}</div>
            </div>
            <div class="transferPwd-item">
                <div class="transferPwd-item-label">
                    <input type="text" v-model="code" placeholder="请输入验证码">
                </div>
                <div class="transferPwd-item-right2">
                    <div class="timeShow2" v-if="!timeShow" @click="vscode">发送验证码</div>
                    <van-count-down v-else :time="time" format="ss" @finish="finish"/>
                </div>
            </div>
            <div class="transferPwd-item">
                <div class="transferPwd-item-label">新密码</div>
                <div class="transferPwd-item-right">
                    <input type="password" v-model="password" placeholder="请输入新密码">
                </div>
            </div>
            <div class="transferPwd-item">
                <div class="transferPwd-item-label">再次输入</div>
                <div class="transferPwd-item-right">
                    <input type="password" v-model="payPassword" placeholder="请再次输入新密码" @blur="onContrast">
                </div>
            </div>
        </div>
        <div class="transferPwd-submit" @click="submitPayPwd">
            确认提交
        </div>
    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {mapActions, mapState} from "vuex";
    import mixin from '@/mixin'
    import {updatePayPassword,sendByMerchantToken} from "../../../api/login";

    export default {
        data() {
            return {
                timeShow: false,
                time: 60 * 1000,
                code: '',
                password: '',
                payPassword: ''
            }

        },
        mixins: [mixin],
        components: {
            Yheader
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            })
        },
        mounted() {
        },
        methods: {
            ...mapActions('user', ["vCode"]),
            vscode() {
                sendByMerchantToken().then(response => {
                    const {code, msg} = response
                    if (code == 200) {
                        this.timeShow = true;
                    }
                    this.$toast(msg);
                }).catch(() => {
                })
            },
            finish() {
                this.timeShow = false;
            },
            /*密码对比*/
            onContrast() {
                if (this.password != this.payPassword) {
                    this.$toast("两次密码不一致")
                    return false
                }
            },
            async submitPayPwd() {
                if (this.password != this.payPassword) {
                    this.$toast("两次密码不一致")
                    return false
                }
                let {code, msg} = await updatePayPassword({
                    phone: this.userInfo.phone,
                    code: this.code,
                    payPassword: this.payPassword
                })
                if (code == 200) {
                    this.$router.go(-1)
                }
                this.$toast(msg)
            }
        }
    }
</script>
<style scoped lang="less">
    .transferPwd {
        min-height: 100vh;
        background: #fff;
        font-family: PingFangSC-Regular, PingFang SC;

        .transferPwd-content {
            padding: 0 20px;

            .transferPwd-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 25px 0 20px 0;
                border-bottom: 1px solid #E2E2E2;

                .transferPwd-item-label {
                    height: 20px;
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 20px;

                    input {
                        font-size: 14px;
                        text-align: left;
                        border: none;

                        &::-webkit-input-placeholder {
                            /* placeholder颜色  */
                            color: #999999;
                            /* placeholder字体大小  */
                            font-size: 14px;
                            /* placeholder位置  */
                            text-align: left;
                        }
                    }
                }

                .transferPwd-item-tpl {
                    height: 20px;
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 20px;
                }

                .transferPwd-item-right {
                    input {
                        font-size: 14px;
                        text-align: right;
                        border: none;

                        &::-webkit-input-placeholder {
                            /* placeholder颜色  */
                            color: #999999;
                            /* placeholder字体大小  */
                            font-size: 14px;
                            /* placeholder位置  */
                            text-align: right;
                        }
                    }
                }

                .transferPwd-item-right2 {
                    width: 70px;
                    text-align: center;
                    position: relative;

                    &::after {
                        content: '';
                        position: absolute;
                        left: -15px;
                        top: -4px;
                        width: 1px;
                        height: 27px;
                        background: #e2e2e2;
                    }

                    .timeShow2 {
                        color: #FF914A;
                    }
                }
            }

        }

        .transferPwd-submit {
            margin: 77px auto 0 auto;
            width: 280px;
            height: 40px;
            background: rgba(255, 101, 9, 1);
            border-radius: 8px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 40px;
            text-align: center;
        }
    }
</style>