<template>
  <div class="ChangePassword">
    <Header title="更改密码" />

    <van-field v-model="userInfo.phone" name="手机号" label="手机号" placeholder="用户名" />
    <van-field v-model="code" center clearable placeholder="请输入短信验证码">
      <!-- <van-button slot="button" size="small" type="primary">发送验证码</van-button> -->
      <div slot="button">
        <div class="Sendcode" v-if="!timeShow" @click="vscode">发送验证码</div>
        <van-count-down v-else :time="time" format="ss" @finish="finish"/>
      </div>
    </van-field>
    <van-field v-model="password" name="新密码" label="新密码" placeholder="新密码" />
    <van-field v-model="password2" name="再次输入" label="再次输入" placeholder="再次输入" />

    <div class="Btn" @click="onSubmit">确认提交</div>
  </div>
</template>
<script>
import Header from "@/Dpage/Components/Header/index";
import {mapActions, mapState} from "vuex";
import {updatePassword} from "../../api/mine";
export default {
  components: {
    Header
  },
  data() {
    return {
      timeShow: false,
      time: 60 * 1000,
      code: "",
      password:'',
      password2:'',
    };
  },
  computed: {
    ...mapState('user', {
      userInfo: state => state.userInfo,
    })
  },
  methods: {
    ...mapActions('user',["vCode"]),
    finish() {
      this.timeShow = false;
    },
    async vscode() {
      this.vCode(this.phone).then(response => {
        const {code,msg} = response
        if (code == 200) {
          this.timeShow = true;
        }else {
          this.$toast(msg);
        }

      }).catch(() => {
      })
    },
    async onSubmit() {
      if (this.code.length<4 ) {
        this.$toast("请输入验证码")
        return false
      }
      if(this.password.length<6){
        this.$toast("请输入正确的密码");
        return false
      }
      if (this.password != this.password2) {
        this.$toast("两次密码不一致")
        return false
      }
      let {code,msg}=await updatePassword({code:this.code,password:this.password})
        if (code == 200) {
          this.$router.go(-1)
        }else {
          this.$toast(msg);
        }
    }
  }
};
</script>
<style lang="less" scoped>
.ChangePassword {
  .Header {
    line-height: 32px;
    color: rgba(255, 255, 255, 1);
    padding-left: 15px;
    width: 375px;
    height: 32px;
    background: linear-gradient(
      180deg,
      rgba(255, 101, 9, 1) 0%,
      rgba(255, 128, 53, 1) 100%
    );
  }
  .Btn {
    color: rgba(255, 255, 255, 1);
    width: 280px;
    height: 40px;
    background: rgba(255, 101, 9, 1);
    border-radius: 8px;
    margin: 0 auto;
    text-align: center;
    line-height: 40px;
    margin-top: 44px;
  }
  .Sendcode {
    width: 70px;
    height: 27px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba(255, 145, 74, 1);
    line-height: 27px;
    padding-left: 15px;
    border-left: 1px solid #e2e2e2;
  }
}
</style>