<template>
	<div class="feedback-record-container">
        	<Header  title="反馈列表" />
		<div class="record-box">
            <div v-show="currentTab == 1">
                <div class="record-item" v-for="(feedback, index) in feedbackRecord" :key="index">
                    <div class="date">
                        <span>{{ feedback.createTime }}</span>
                    </div>
                    <div class="status">
                        <b class="title">意见反馈</b>
                        <b class="operate" @click="$router.push({name: 'opinionIndex',params:{id:feedback.id}});">查看</b>
                    </div>
                </div>
            </div>

		</div>
	</div>
</template>

<script>
	import {getOpinionList} from "@/api/mine";
import Header from "@/Dpage/Components/Header/index";
	export default {
		components: {
            Header
        },
		data() {
			return {
				tabTitle: ['意见反馈', '抽佣申请'],
				fontColor: "#999",
				lineColor: "#5192FF",
				bgColor: "#fff",
				currentTab: 1,
				feedbackRecord: [{
                    createTime: "2020-03-01 22:51:06",
                    id: 1
					},
					{
                        createTime: "2020-03-01 22:51:06",
                        id: 1
					},
					{
                        createTime: "2020-03-01 22:51:06",
                        id: 1
					}
				]
			}
		},
		async mounted() {
		    this.queryUserIdeaListByType()
		},
		methods: {
		    async queryUserIdeaListByType(){
                let {code, data} = await getOpinionList({type:this.currentTab})
                if (code == 200) {
                    this.feedbackRecord = data || []
                }
            },
			changeTab(index) {
				this.currentTab = index+1;
                this.queryUserIdeaListByType()
			},
		},
	}
</script>

<style lang="less" scoped>

	.feedback-record-container {
        min-height: 100vh;
        background: #f9f9f9;
		.record-box {
			box-sizing: border-box;
			padding: .33rem;
			padding-top: 0;
            
            
			.record-item {
				display: flex;
				flex-direction: column;
				margin-top: .55rem;

				.date {
					font-size: .30rem;
					color: #666;
					text-align: center;
				}

				.status {
					box-sizing: border-box;
					padding: .33rem;
					display: flex;
					justify-content: space-between;
					align-items: center;
					border-radius: .14rem;
					background: #fff;
					margin-top: .11rem;
                    font-size: .38rem;
					.title {
						color: #333;
					}

					.operate {
						color: #5192FF;
					}
				}
			}
		}
	}
</style>
