import { apiAxios,apiAxiosJson } from './axios.js'
//商户账单查询接口
export const queryBillList = (data) => {
    return apiAxios('post', '/api/merchant/queryBillList', data)
}
export const orderSuccess = (data) => {
    return apiAxios('post', '/api/order/success', data)
}
export const orderFail = (data) => {
    return apiAxios('post', '/api/order/fail', data)
}
//修改订单备注
export const updateOrderRemark = (data) => {
    return apiAxios('post', '/api/order/updateOrderRemark', data)
}


