<template>
  <div class="login">
   
    <div class="login-head">
      <div :class="[!loginActive?'login-head-active':'']" @click="loginActive=false">用户登录</div>
      <div :class="[loginActive?'login-head-active':'']" @click="loginActive=true">用户注册</div>
    </div>
    <div class="login-content" v-if="!loginActive">
      <div class="login-user">
        <input type="number" v-model="phone" placeholder="您的手机号" />
      </div>
      <div class="login-user">
        <input type="password" v-model="password" placeholder="请输入密码" />
      </div>
      <!--<div class="login-cut" @click="$router.push({name:'loginVerification'})">-->
      <!--<div class="login-cut-item">-->
      <!--<img src="../assets/transform.png" alt/>-->
      <!--</div>-->
      <!--<span>切换验证码登录</span>-->
      <!--</div>-->
      <div class="login-cut" @click="$router.push({name:'forgetPwd'})">
        <div class="login-cut-item">
          <img src="../assets/transform.png" alt />
        </div>
        <span>忘记密码</span>
      </div>
      <div class="login-submit" @click="onLogin">登录</div>
    </div>
    <div class="login-content" v-else>
      <div class="login-user">
        <input type="number" v-model="phone" placeholder="您的手机号" />
      </div>
      <div class="login-user">
        <input type="number" v-model="code" placeholder="请输入验证码" />
        <div class="login-right2">
          <div class="timeShow2" v-if="!timeShow" @click="handletimeShow">发送验证码</div>
          <van-count-down v-else :time="time" format="ss" @finish="finish" />
        </div>
      </div>
      <div class="forgetPwd-user">
        <div class="forgetPwd-user-label">设置密码</div>
        <input :type="passwordBoole?'text':'password'" v-model="password2" />
        <van-icon
          class="forgetPwd-eye"
          :name="passwordBoole?'eye-o':'closed-eye'"
          @click.stop="passwordBoole=!passwordBoole"
        />
        <!--<van-icon name="" />-->
      </div>
      <div class="login-user">
        <input type="text" v-model="Invitation_code" placeholder="请输入邀请码(可不填)" />
      </div>
      <div class="login-agreement">
        <van-checkbox v-model="checked" icon-size="12px" checked-color="#FF6509">
          <span class="login-agreement1">已阅读并同意</span>
          <span class="login-agreement2">《用户注册协议》</span>
        </van-checkbox>
      </div>
      <div class="login-submit2" @click="submit2">注册</div>
    </div>
  </div>
</template>
<script>
import {
  vscode,
  merchantCodeLogin,
  merchantPartnerCodeRegister
} from "@/api/login";
import mixin from "@/mixin";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      phone: "",
      password: "",
      password2: "",
      passwordBoole: false,
      Invitation_code: "" /*邀请码*/,
      code: "",
      loginActive: false,
      timeShow: false,
      time: 60 * 1000,
      checked: true,
      defend: false,
      inviteKey: "",
    };
  },
  mixins: [mixin],
  mounted() {
    var code = this.getUrlParam("code");
    this.inviteKey = this.getUrlParam("inviteKey");
    if (code) {
      this.loginActive = true;
      this.Invitation_code = code;
    }
    if (this.inviteKey) {
      this.loginActive = true;
      this.Invitation_code = this.inviteKey;
    }
    //判断账号密码是否保存本地
    if (localStorage.getItem("loginForm")) {
      const loginForm = JSON.parse(localStorage.getItem("loginForm"));
      this.phone = loginForm.phone;
      this.password = loginForm.password;
    }
  },
  watch: {
    loginActive: function(newVal, oldVal) {
      this.password = "";
      this.password2 = "";
      this.code = "";
    }
  },
  methods: {
    ...mapActions("user", ["login", "loginEnroll", "vCode"]),
   
    onLogin() {
      if (this.defend) {
        this.defend = true;
        return;
      }

      /*验证方法执行 */
      // if (!this.vPhone(this.phone)) {
      //     return false
      // }
      if (this.password.length < 6) {
        this.$toast("请输入正确的密码");
        return false;
      }
      this.login({ phone: this.phone, password: this.password })
        .then(response => {
          this.defend = false;
          const { code, data, msg } = response;
          if (code == 200) {
            if (data.hasPassword) {
              this.$toast("登录成功");
              //保存账号密码到本地
              localStorage.setItem(
                "loginForm",
                JSON.stringify({
                  phone: this.phone,
                  password: this.password
                })
              );
              this.$router.replace({ name: "publish" });
            } else {
              this.$toast("跳转设置密码");
              this.$router.replace({ name: "ChangePassword" });
            }
          } else {
            this.$toast(msg);
          }
        })
        .catch(() => {
          this.defend = false;
        });
    },
    async submit2() {
      if (this.defend) {
        this.defend = true;
        return;
      }

      let code = this.code;
      let phone = this.phone;
      if (phone.length != 11) {
        this.$toast("请输入正确手机号码");
        return;
      }
      if (code.length < 4) {
        this.$toast("请输入验证码");
        return;
      }
      if (code.Invitation_code == "") {
        this.$toast("请输入邀请码");
        return;
      }

      //商户邀请链接注册接口
      if (this.inviteKey) {
        merchantPartnerCodeRegister({
          phone: this.phone,
          code: this.code,
          inviteKey: this.$route.query.inviteKey,
          password: this.password2
        }).then(response => {
            this.defend = false;
            const { code, data, msg } = response;
            if (code == 200) {
              if (data.hasPassword) {
                this.$toast("注册成功");
                this.$router.replace({ name: "publish" });
              } else {
                this.$toast("跳转设置密码");
                this.$router.replace({ name: "ChangePassword" });
              }
            } else {
              this.$toast(msg);
            }
          })
          .catch(() => {
            this.defend = false;
          });
      } else {
        this.loginEnroll({
          phone: this.phone,
          code: this.code,
          shareCode: this.Invitation_code,
          password: this.password2
        })
          .then(response => {
            this.defend = false;
            const { code, data, msg } = response;
            if (code == 200) {
              if (data.hasPassword) {
                this.$toast("注册成功");
                this.$router.replace({ name: "publish" });
              } else {
                this.$toast("跳转设置密码");
                this.$router.replace({ name: "ChangePassword" });
              }
            } else {
              this.$toast(msg);
            }
          })
          .catch(() => {
            this.defend = false;
          });
      }
    },

    async handletimeShow() {
      let phone = this.phone;
      if (phone.length != 11) {
        this.$toast("请输入正确手机号码");
        return;
      }
      this.vCode(this.phone)
        .then(response => {
          const { code, msg } = response;
          if (code == 200) {
            this.timeShow = true;
          } else {
            this.$toast(msg);
          }
        })
        .catch(() => {});
    },
    finish() {
      this.timeShow = false;
    }
  }
};
</script>
<style scoped lang="less">
	
.login {
  min-height: 100vh;
  font-family: PingFangSC-Regular, PingFang SC;

  .login-head {
    margin-top: 60px;
    width: 375px;
    height: 44px;
    background: rgba(255, 255, 255, 1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1;

    div {
      width: 188px;
      height: 44px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(102, 102, 102, 1);
      line-height: 44px;
      text-align: center;
    }

    .login-head-active {
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        margin-left: -24px;
        width: 48px;
        height: 3px;
        background: #ff6509;
      }
    }
  }

  .login-content {
    margin-top: 22px;
    padding: 0 31px;

    .forgetPwd-user {
      width: 313px;
      border-bottom: 1px solid #d8d8d8;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .forgetPwd-user-label {
        width: 68px;
        height: 23px;
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 23px;
      }

      .forgetPwd-eye {
        font-size: 16px;
        height: 23px;
        width: 23px;
        line-height: 23px;
      }

      input {
        width: 170px;
        height: 23px;
        font-size: 16px;
        font-weight: 400;
        color: #000000;
        line-height: 23px;
        padding: 23px 5px;
        margin-right: 24px;
        border: none;
        text-align: right;

        &::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #999999;
          /* placeholder字体大小  */
          font-size: 16px;
          /* placeholder位置  */
          text-align: right;
        }
      }
    }

    .login-user {
      width: 313px;
      border-bottom: 1px solid #d8d8d8;
      display: flex;
      justify-content: space-between;
      align-items: center;

      input {
        width: 313px;
        height: 23px;
        font-size: 16px;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
        line-height: 23px;
        padding: 23px 2px;
        border: none !important;

        &::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #999999;
          /* placeholder字体大小  */
          font-size: 16px;
          /* placeholder位置  */
          text-align: left;
        }
      }

      .login-right2 {
        width: 120px;
        text-align: center;
        height: 23px;
        font-size: 16px;
        font-weight: 400;
        color: rgba(255, 101, 9, 1);
        line-height: 23px;
        position: relative;

        &::after {
          content: "";
          position: absolute;
          left: -15px;
          top: -4px;
          width: 1px;
          height: 27px;
          background: #e2e2e2;
        }

        .timeShow2 {
          color: #ff914a;
        }
      }
    }

    .login-cut {
      margin-top: 20px;
      height: 48px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(255, 101, 9, 1);
      line-height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;

      .login-cut-item {
        margin-top: 5px;
        margin-right: 9px;

        img {
          width: 16px;
          height: 16px;
        }
      }
    }

    .login-agreement {
      margin-top: 18px;
      height: 48px;
      font-size: 13px;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 48px;

      .login-agreement1 {
        margin-left: 10px;
      }

      .login-agreement2 {
        color: #ff6509;
      }
    }

    .login-submit {
      width: 280px;
      height: 40px;
      background: rgba(255, 101, 9, 1);
      border-radius: 8px;
      margin: 94px auto 0 auto;
      font-size: 14px;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 40px;
      text-align: center;
    }

    .login-submit2 {
      width: 280px;
      height: 40px;
      background: rgba(255, 101, 9, 1);
      border-radius: 8px;
      margin: 44px auto 0 auto;
      font-size: 14px;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 40px;
      text-align: center;
    }
  }
}
</style>