<template>
    <div class="recommend">
        <div class="Header">
            <div>
                <slot name="Left">
                    <van-icon name="arrow-left" @click="$router.go(-1)" class="Left" />
                </slot>
            </div>
            <div class="Title">推荐下级</div>
            <div class="right">
            </div>
        </div>
        <div class="haoyou">邀请好友</div>
        <div class="duoduo">大量任务 ，佣金多多</div>
        <div class="recontent">
            <div class="youcode">您的邀请码</div>
            <div class="code">{{userInfo.merchantCode}}</div>
            <div class="url" @click="copy">{{url}}</div>
            <div class="click">点击链接复制</div>
            <div class="click">快邀请好友来王牌赚钱吧！</div>
            <div class="qrcode">
                <div id="qrcode" ></div>
            </div>
        </div>
    </div>
</template>
<script>
    import {handleClipboard} from './index';
    import QRCode  from "qrcodejs2"
    import {getInviteCode} from "../../api/mine";
    import {mapState} from "vuex";
    export default {
        data(){
            return{
                url:""
            }
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            }),

        },
        async mounted() {

            let {code,data}=await getInviteCode()
            if(code==200){
                this.url=data
                this.qrcode(data)
            }
        },
        methods:{
            copy(){
                handleClipboard(this.url, event, () => {
                    alert('已经复制在剪贴版')
                }, () => {
                    alert('复制失败！')
                })
            },
            qrcode (data) {
                let qrcode = new QRCode('qrcode', {
                    width: 94,
                    height: 94,        // 高度
                    text: data,   // 二维码内容
                    // render: 'canvas' ,   // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
                    // background: '#f0f',   // 背景色
                    // foreground: '#ff0'    // 前景色
                })
                console.log(qrcode)
            }
        }
    }
</script>
