<template>
    <div class="Dataquery">
        <Header title="数据查询"/>
        <div class="portrait">
            <van-image round width="100%" height="100%" src="https://img.yzcdn.cn/vant/cat.jpeg"/>
        </div>
        <div class="Name">账户 {{userInfo.nickname}}</div>
        <div class="Header_top">
            <div class="Boder">
                <van-field
                        right-icon="notes-o"
                        readonly
                        clickable
                        name="datetimePicker"
                        :value="value"
                        label="选择日期"
                        placeholder="点击选择时间"
                        @click="showPicker = true"
                ></van-field>
            </div>
            <van-popup v-model="showPicker" position="bottom">
                <van-datetime-picker
                        type="date"
                        v-model="currentDate"
                        @confirm="confirm"
                        @cancel="showPicker = false"
                ></van-datetime-picker>
            </van-popup>
        </div>
        <div class="index_bottom">
            <div class="list">
                <div>
                    <div>{{statisData.allNum || 0}}</div>
                    <div>总下单</div>
                </div>
                <div>
                    <div>{{statisData.successNum|| 0}}</div>
                    <div>日成功</div>
                </div>
                <div>
                    <div>{{statisData.successRate|| 0}}</div>
                    <div>日成功率</div>
                </div>
            </div>
            <div class="list lb">
                <div>
                    <div>{{statisData.failNum|| 0}}</div>
                    <div>日失败</div>
                </div>
                <div>
                    <div>{{statisData.failRate|| 0}}</div>
                    <div>日失败率</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    const formatNumber = n => {
    n = n.toString()
    return n[1] ? n : '0' + n
    }
    import Header from "@/Dpage/Components/Header/index";
    import {statisticsSearch} from "../../api/mine";
    import {mapState} from "vuex";

    export default {
        components: {
            Header
        },
        data() {
            return {
                value: "",
                showPicker: false,
                currentDate: new Date(),
                statisData: {}
            };
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            }),
        },
        async mounted() {
            // this.value = this.formatDate(new Date());
            this.statisticsSearch()
        },
        methods: {
            async statisticsSearch() {
                let obj =  {}
                if(this.value){
                    obj =  {dateTime: this.value}
                }
               
                let {code, data} = await statisticsSearch(obj)
                if (code == 200) {
                    this.statisData = data
                }
            },
            confirm(time) {
                this.value = this.formatDate(time);
                this.currentDate  = time;
                this.showPicker = false;
                this.statisticsSearch()
            },
            formatDate(time = "") {
                let date = new Date(time);
                const year = date.getFullYear()
                const month = date.getMonth() + 1
                const day = date.getDate()
                const hour = date.getHours()
                const minute = date.getMinutes()
                const second = date.getSeconds()
                return [year, month, day].map(formatNumber).join('-') ;
                // return new Date(time).toLocaleDateString()
            }
        }
    };
</script>
<style lang="less" scoped>
    .Dataquery {
        height: 100vh;
        background: #fbf8f9;

        .portrait {
            height: 64px;
            width: 64px;
            margin: 0 auto;
            margin-top: 18px;
        }

        .Name {
            font-size: 14px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: rgba(51, 51, 51, 1);
            line-height: 20px;
            text-align: center;
            font-size: 12px;
        }

        .Header_top {
            margin: 0 15px;
            margin-top: 18px;
        }

        .index_bottom {
            background: #fff;
            width: 345px;

            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 1px 8px 0px rgba(246, 246, 246, 1);
            border-radius: 8px;
            margin: 0 auto;
            margin-top: 14px;

            .lb {
                padding-bottom: 18px;
            }

            .list {
                display: flex;
                padding: 12px 0;
                box-sizing: border-box;

                > div {
                    width: 115px;

                    > div {
                        width: 100%;
                        text-align: center;
                    }

                    > div:nth-child(1) {
                        color: rgba(255, 101, 9, 1);
                        font-size: 16px;
                        margin-bottom: 5px;
                    }

                    > div:nth-child(2) {
                        font-size: 13px;
                    }
                }
            }
        }
    }
</style>