<template>
  <div class="Subordinate">
    <Header title="充值记录" />
    <div class="Header_top">
      <div class="Boder">
        <van-field
          right-icon="notes-o"
          readonly
          clickable
          name="datetimePicker"
          :value="value"
          label="选择日期"
          placeholder="点击选择时间"
          @click="showPicker = true"
        />
      </div>
      <van-popup v-model="showPicker" position="bottom">
        <van-datetime-picker
          type="date"
          v-model="currentDate"
          @confirm="confirm"
          @cancel="showPicker = false"
        />
      </van-popup>
    </div>
    <div class="Index">
      <div class="List"  v-for="(item,index) in list" :key="index">
        <div class="List_top">
          <div class="date" v-text="item.topUpTime">2020/04/09 11:30:30</div>
          <div class="price" v-text="item.amount/100+'元'">199元</div>
        </div>
        <div class="List_bottom">
          <div class="B">充值</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { formatDate } from "@/Dpage/Utill/utill";
 import { queryTopUpList } from "@/api/mine";

import Header from "@/Dpage/Components/Header/index";
export default {
  components: {
    Header
  },
  data() {
    return {
      value: "",
      showPicker: false,
      currentDate: new Date(),
      list:[]
    };
  },
  mounted() {
    this.value = formatDate(new Date());
    this.getList();
  },
  methods: {
    getList(){
      var self = this;
      queryTopUpList({dateTime:self.value}).then(res=>{
        console.log(res)
        if(res.code == 200){
          this.list = res.data
        }
      })
     
    },
    confirm(time) {
      this.value = formatDate(time);
      
      this.showPicker = false;
      this.getList();
    }
  }
};
</script>
<style lang="less" scoped>
.Index {
  min-height: calc(100vh - 90px);
  padding: 15px;
  background: #fbf8fb;
  box-sizing: border-box;
  font-size: 12px;
  .List {
    width: 345px;

    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 8px 0px rgba(246, 246, 246, 1);
    border-radius: 8px;

    padding-top: 9px;
    box-sizing: border-box;
    .List_top {
      display: flex;
      padding: 0 16px;
      padding-bottom: 9px;
      align-items: center;
      justify-content: space-between;
      .date {
        font-size: 14px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: rgba(51, 51, 51, 1);
      }
      .price {
        color: #00c929;
        position: relative;
        top: 0px;
      }
    }
    .List_bottom {
      padding: 9px 0;
      .B {
        padding: 0 16px;
        font-size: 13px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);
      }
      border-top: 1px solid #ededed;
    }
  }
}
</style>