<template>
    <div class="transfer">
        <Yheader title="余额转移" :backBoole="true" @_goBack="$router.go(-1)"></Yheader>
        <div class="transfer-balance">
            <div class="transfer-user-balance">账户余额</div>
            <div class="transfer-user-price"><span>¥</span>{{userInfo.balance.toFixed(2)}}</div>
            <div class="transfer-user-submit" @click="$router.push({name:'transferPwd'})">设置转移密码</div>
        </div>
        <div class="transfer-content">
            <div class="transfer-content-item">
                <div class="transfer-citem-child">接受款项的账号</div>
                <div class="transfer-citem-child2">
                    <input type="text" v-model="phone" placeholder="请输入账号">
                </div>
            </div>
            <div class="transfer-content-item">
                <div class="transfer-citem-child">要转移的金额</div>
                <div class="transfer-citem-child2">
                    <input type="text" v-model="amount" placeholder="请填写">
                    <span>元</span>
                </div>
            </div>
            <div class="transfer-content-item">
                <div class="transfer-citem-child">请输入转移密码</div>
                <div class="transfer-citem-child2">
                    <input type="password" v-model="payPassWord" placeholder="请输入转移密码">
                </div>
            </div>
        </div>
        <div class="transfer-tip">
            <span>【提示】</span>
            可以把当前账号的金额转移到其他账户(仅限{{name}}内部用户，非支付宝和银行账户)，转移后金额不能撤销，请在转移前确认对方账号是否正确，{{name}}平台不承担资金损失!
        </div>
        <div class="transfer-submit" @click="onsubmit">
            确认提交
        </div>
    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {mapMutations, mapState} from "vuex";
    import {transferBalance} from "../../../api/mine";
    export default {
        data() {
            return {
                phone: '',
                amount: '',
                payPassWord: '',
                name:process.env.VUE_APP_API_Name
            }

        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            }),

        },
        components: {
            Yheader
        },
        mounted() {
            console.log(this.name)
        },
        methods: {
            ...mapMutations('user', ['SET_BALANCE']),
            async onsubmit() {
                if (this.phone == '') {
                    this.$toast("请输入账号")
                    return false;
                }
                if (this.amount == 0 || this.amount == '') {
                    this.$toast('请输入金额')
                    return false;
                }
                if (this.payPassWord == '' || this.payPassWord.length < 6) {
                    this.$toast('请输入转移密码并且超过6位数')
                    return false;

                }
                let {code, msg, data} = await transferBalance({
                    phone: this.phone,
                    amount: this.amount*100,
                    payPassWord: this.payPassWord
                })
                if (code == 200) {
                    this.$toast(msg)
                    this.SET_BALANCE(data.remainBalance/100)
                    this.phone = ''
                    this.amount = ''
                    this.payPassWord = ''
                }
            }
        }
    }
</script>
<style scoped lang="less">
    .transfer {
        min-height: 100vh;
        background: #F9F9F9;
        font-family: PingFangSC-Regular, PingFang SC;

        .transfer-balance {
            margin: 14px 22px 0 22px;
            width: 331px;
            height: 49px;
            background: url("../assets/bgbalance.png") no-repeat;
            background-size: 100% 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;

            .transfer-user-balance {
                margin-left: 16px;
                margin-right: 32px;
                width: 52px;
                height: 48px;
                font-size: 13px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 48px;
            }

            .transfer-user-price {
                width: 163px;
                height: 48px;
                font-size: 16px;
                font-weight: 500;
                color: rgba(255, 255, 255, 1);
                line-height: 48px;

                span {
                    font-size: 12px;
                }
            }

            .transfer-user-submit {
                margin-right: 16px;
                width: 96px;
                height: 24px;
                background: rgba(255, 255, 255, 1);
                border-radius: 12px;
                font-size: 12px;
                font-weight: 400;
                color: rgba(255, 146, 25, 1);
                line-height: 24px;
                text-align: center;
            }
        }

        .transfer-content {
            padding: 0 22px;
            width: 331px;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.08);

            .transfer-content-item {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .transfer-citem-child {
                    width: 98px;
                    height: 48px;
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 48px;
                    border-bottom: 1px solid #ededed;
                }

                .transfer-citem-child2 {
                    input {
                        text-align: right;
                        font-size: 14px;
                        border: none;

                        &::-webkit-input-placeholder {
                            /* placeholder颜色  */
                            color: #999999;
                            /* placeholder字体大小  */
                            font-size: 14px;
                            /* placeholder位置  */
                            text-align: right;
                        }
                    }

                    span {
                        width: 14px;
                        height: 48px;
                        font-size: 14px;
                        font-weight: 400;
                        color: rgba(51, 51, 51, 1);
                        margin-left: 16px;
                        line-height: 48px;
                    }
                }
            }
        }

        .transfer-tip {
            margin: 14px 22px 0 22px;
            width: 331px;
            height: 66px;
            font-size: 11px;
            font-weight: 500;
            color: #666666;
            line-height: 22px;

            span {
                color: #FF6509;
            }
        }

        .transfer-submit {
            margin: 77px auto 0 auto;
            width: 280px;
            height: 40px;
            background: rgba(255, 101, 9, 1);
            border-radius: 8px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 40px;
            text-align: center;
        }
    }
</style>