<template>
    <div class="balance">
        <Yheader title="余额明细" :backBoole="true" :shadow="false" @_goBack="$router.go(-1)"></Yheader>
        <div class="balance-head-demo"></div>
        <div class="balance-head" @click="dateShow=true">
            <div class="bh-title">选择日期</div>
            <div class="bh-date">
                <span>{{getDate(currentDate)}}</span>
                <img src="../assets/calendar.png" alt="">
            </div>
        </div>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh" class="balance-content">
            <van-list
                    :immediate-check="true"
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"
                    @load="onLoad"
            >
                <div v-for="(item,index) in list" :key="index" class="balance-content-item">
                    <div class="bc-item">
                        <div class="bc-item-date">{{item.createTime}}</div>
                        <div class="bc-item-price" v-if="item.type==1">+{{item.changeAmount}}元</div>
                        <div class="bc-item-price bc-item-price2" v-if="item.type==2">-{{item.changeAmount}}元</div>
                    </div>
                    <div class="bc-item-tip">{{item.subscribe}}</div>
                </div>
            </van-list>
        </van-pull-refresh>
        <van-popup
                v-model="dateShow"
                position="bottom"
                :style="{ height: '40%' }"
        >
            <van-datetime-picker
                    @cancel="dateShow=false"
                    @confirm="confirm"
                    v-model="currentDate"
                    type="date"
                    :min-date="minDate"
                    :max-date="maxDate"
            ></van-datetime-picker>
        </van-popup>

    </div>
</template>
<script>
    import Yheader from '../Yheader/index'
    import {queryMyBalanceList} from "../../../api/login";

    var list = {
        "content": [
            {
                "id": "122",
                "subscribe": "测试退款",
                "userId": null,
                "userType": null,
                "type": 2,
                "changeAmount": 1,
                "payType": null,
                "subType": null,
                "refundStatus": null,
                "refundMsg": null,
                "outRefundNo": null,
                "orderNo": null,
                "serialNo": null,
                "orderType": null,
                "status": null,
                "beforeBanlance": null,
                "updateTime": null,
                "createTime": "2019-12-17 11:15:44"
            },
            {
                "id": "14",
                "subscribe": "测试退款",
                "userId": null,
                "userType": null,
                "type": 2,
                "changeAmount": 1,
                "payType": null,
                "subType": null,
                "refundStatus": null,
                "refundMsg": null,
                "outRefundNo": null,
                "orderNo": null,
                "serialNo": null,
                "orderType": null,
                "status": null,
                "beforeBanlance": null,
                "updateTime": null,
                "createTime": "2019-12-17 11:15:44"
            }
        ],
        "pageNo": 2,
        "pageSize": 2,
        "totalCount": 2,
        "totalPage": 2,
        "beginPageIndex": 0,
        "endPageIndex": 0,
        "sort": null,
        "order": null,
        "isLimit": 0,
        "param": {
            "userType": 2,
            "userId": 19
        },
        "beginIndex": 2
    }
    export default {
        data() {
            return {
                list: [],
                loading: false,
                finished: false,
                refreshing: false,
                dateShow: false,
                minDate: new Date(1970, 1, 1),
                maxDate: new Date(),
                currentDate: new Date(),
                pageno: 1,
                pageSize: 10,
                totalCount: 0
            }
        },
        components: {
            Yheader
        },
        mounted() {
            this.queryMyBalanceList()
        },
        methods: {
            async queryMyBalanceList() {
                let that = this
                let {code, data} = await queryMyBalanceList({pageno: this.pageno, pageSize: this.pageSize})
                if (code == 200) {
                    if (this.pageno == 1) {
                        this.list = [];

                    }
                    this.refreshing = false
                    data.content.forEach(function (item) {
                        item.changeAmount=item.changeAmount/100
                        that.list.push(item);
                    })
                    this.totalCount = data.totalCount

                }
            },
            onLoad() {
                setTimeout(() => {
                    this.loading = false;
                    this.pageno += 1
                    if (this.list.length >= this.totalCount) {
                        this.finished = true;
                    } else {
                        this.queryMyBalanceList()
                    }
                }, 1000);
            },
            onRefresh() {
                // 将 loading 设置为 true，表示处于加载状态
                setTimeout(() => {
                    this.pageno = 1
                    this.queryMyBalanceList()
                }, 1000);

            },
            confirm() {
                this.dateShow = false
            },
            getDate(val) {
                return new Date(val).toLocaleDateString()
            }
        }
    }
</script>
<style lang="less" scoped>
    .balance {
        min-height: 100vh;
        background: #F9F9F9;
        font-family: PingFangSC-Regular, PingFang SC;

        .balance-head-demo {
            height: 44px;
        }

        .balance-head {
            position: fixed;
            top: 44px;
            left: 0;
            width: 375px;
            height: 44px;
            background: rgba(255, 255, 255, 1);
            display: flex;
            justify-content: flex-start;
            align-items: center;
            z-index: 1;

            .bh-title {
                margin-left: 15px;
                margin-right: 40px;
                width: 56px;
                height: 44px;
                font-size: 14px;
                font-weight: 400;
                color: rgba(51, 51, 51, 1);
                line-height: 44px;

            }

            .bh-date {
                width: 264px;
                height: 44px;
                font-size: 14px;
                font-weight: 400;
                color: rgba(51, 51, 51, 1);
                line-height: 44px;
                position: relative;

                img {
                    position: absolute;
                    right: 15px;
                    top: 50%;
                    margin-top: -7px;
                    width: 14px;
                    height: 14px;
                }
            }
        }

        .balance-content {
            height: calc(100vh - 106px) !important;
            overflow-y: scroll;
            padding: 14px 15px;

            .balance-content-item {
                width: 345px;
                height: 80px;
                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 1px 8px 0px rgba(246, 246, 246, 1);
                border-radius: 8px;
                margin-bottom: 14px;

                .bc-item {
                    display: flex;
                    justify-content: space-between;
                    align-self: center;
                    border-bottom: 1px solid #EDEDED;

                    .bc-item-date {
                        width: 139px;
                        height: 36px;
                        font-size: 14px;
                        font-weight: 500;
                        color: rgba(51, 51, 51, 1);
                        line-height: 36px;
                        padding-left: 16px;
                    }

                    .bc-item-price {
                        height: 36px;
                        font-size: 14px;
                        font-weight: 500;
                        color: rgba(0, 201, 41, 1);
                        line-height: 36px;
                        padding-right: 16px;
                    }

                    .bc-item-price2 {
                        color: #FF6509;
                    }
                }

                .bc-item-tip {
                    padding-left: 16px;
                    height: 44px;
                    font-size: 13px;
                    font-weight: 400;
                    color: rgba(102, 102, 102, 1);
                    line-height: 44px;
                }
            }
        }
    }
</style>