<template>
    <div class="information">
        <Header title="账户资料"/>
        <div class="Header">请编辑你的信息</div>
        <van-field v-model="userInfo.phone" name="账号" label="账号" disabled placeholder="用户名"/>
        <van-field v-model="userInfo.upMerchant" name="用户名" label="我的上级" disabled placeholder="我的上级"/>
        <van-field v-model="userInfo.nickname" name="用户名" label="昵称" placeholder="昵称"/>
        <van-field v-model="userInfo.weixin" name="用户名" label="微信" placeholder="请输入微信号"/>
        <van-field v-model="userInfo.qq" name="用户名" label="QQ" placeholder="请输入QQ号"/>
        <van-cell title="更改密码" is-link @click="$router.push({path: '/Personal/ChangePassword'});"/>
        <div class="Btn" @click="onSubmit">确认提交</div>
    </div>
</template>
<script>
    import Header from "@/Dpage/Components/Header/index";
    import {mapActions, mapState} from "vuex";

    export default {
        components: {
            Header
        },
        data() {
            return {
                WX: "",
                QQ: "",
                username: ""
            };
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            })
        },
        methods: {
            ...mapActions('user', ["updateInfo2"]),
            onSubmit() {
                this.updateInfo2({
                    nickname: this.userInfo.nickname,
                    upMerchant: this.userInfo.upMerchant,
                    weixin: this.userInfo.weixin,
                    qq: this.userInfo.qq,
                }).then(response => {
                    const { msg} = response
                    this.$toast(msg);
                }).catch(() => {
                })
            }
        }
    };
</script>
<style lang="less" scoped>
    .information {
        .Header {
            line-height: 32px;
            color: rgba(255, 255, 255, 1);
            padding-left: 15px;
            width: 375px;
            height: 32px;
            background: linear-gradient(
                    180deg,
                    rgba(255, 101, 9, 1) 0%,
                    rgba(255, 128, 53, 1) 100%
            );
        }

        .Btn {
            color: rgba(255, 255, 255, 1);
            width: 280px;
            height: 40px;
            background: rgba(255, 101, 9, 1);
            border-radius: 8px;
            margin: 0 auto;
            text-align: center;
            line-height: 40px;
            margin-top: 44px;
        }
    }
</style>