<template>
    <div class="loginVerification">
        <div @click="$router.go(-1)" class="back">
            <van-icon name="arrow-left" color="#000"/>
        </div>
        <div class="loginVerification-log">验证码登录</div>
        <div class="transferDetail-content">
            <div class="transferDetail-user">
                <input type="number" v-model="phone" placeholder="您的手机号"/>
            </div>
            <div class="transferDetail-user">
                <input type="number" v-model="code" placeholder="请输入验证码"/>
                <div class="transferDetail-right2">
                    <div class="timeShow2" v-if="!timeShow" @click="vscode">发送验证码</div>
                    <van-count-down v-else :time="time" format="ss" @finish="finish"/>
                </div>
            </div>
            <div class="transferDetail-submit" @click="submitLogin">登录</div>
        </div>
    </div>
</template>
<script>
    import {mapActions} from "vuex";

    export default {
        data() {
            return {
                loginActive: true,
                timeShow: false,
                time: 60 * 1000,
                checked: true,
                phone: '',
                code:''
            };
        },
        mounted() {
        },
        methods: {
            ...mapActions('user',["vCode",'login2']),
            finish() {
                this.timeShow = false;
            },
            async vscode() {
                this.vCode(this.phone).then(response => {
                    const {code,msg} = response
                    if (code == 200) {
                        this.timeShow = true;
                    }else {
                        this.$toast(msg);
                    }

                }).catch(() => {
                })
            },
            submitLogin() {
                /*验证方法执行 */
                // if(!this.vPhone(this.phone)){
                //    return false
                // }
                if (this.code.length < 4) {
                    this.$toast("请输入验证码");
                    return false;
                }
                this.login2({phone: this.phone, code: this.code,name:'xx'}).then(response => {
                    const {code,data} = response
                    if (code == 200) {

                        if(data.hasPassword){
                            this.$toast('登录成功')
                            this.$router.replace({name: 'publish'})
                        }else {
                            this.$toast('跳转设置密码')
                            this.$router.replace({name: 'ChangePassword'})
                        }
                    }
                }).catch(() => {
                })
            },
        }
    };
</script>
<style scoped lang="less">
    .loginVerification {
        min-height: 100vh;
        font-family: PingFangSC-Regular, PingFang SC;

        .back {
            position: absolute;
            left: 31px;
            top: 62px;
            transform: translateX(-50%);
            width: 10px;
            height: 10px;
            color: #fff;
            font-size: 20px;
        }

        .loginVerification-log {
            margin-top: 129px;
            margin-bottom: 12px;
            text-align: center;
            height: 31px;
            font-size: 22px;
            font-weight: 500;
            color: rgba(51, 51, 51, 1);
            line-height: 31px;
        }

        .transferDetail-content {
            margin-top: 22px;
            padding: 0 31px;

            .transferDetail-user {
                width: 313px;
                border-bottom: 1px solid #d8d8d8;
                display: flex;
                justify-content: space-between;
                align-items: center;

                input {
                    width: 313px;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 23px;
                    padding: 23px 2px;
                    border: none;

                    &::-webkit-input-placeholder {
                        /* placeholder颜色  */
                        color: #999999;
                        /* placeholder字体大小  */
                        font-size: 16px;
                        /* placeholder位置  */
                        text-align: left;
                    }
                }

                .transferDetail-right2 {
                    width: 120px;
                    text-align: center;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(255, 101, 9, 1);
                    line-height: 23px;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        left: -15px;
                        top: -4px;
                        width: 1px;
                        height: 27px;
                        background: #e2e2e2;
                    }

                    .timeShow2 {
                        color: #ff914a;
                    }
                }
            }

            .transferDetail-submit {
                width: 280px;
                height: 40px;
                background: rgba(255, 101, 9, 1);
                border-radius: 8px;
                margin: 112px auto 0 auto;
                font-size: 14px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 40px;
                text-align: center;
            }
        }
    }
</style>