<template>
    <div class="Bill">
        <Header title="账单">
            <div slot="Left"></div>
            <div slot="Right">
                <van-icon name="replay" class="YanSe"/>
                <span class="font" @click="queryBillList2">刷新</span>
            </div>
        </Header>

        <div class="HeaderIndex">
            <div class="Boder">
                <van-field
                        right-icon="notes-o"
                        readonly
                        clickable
                        name="datetimePicker"
                        :value="value"
                        label="选择日期"
                        placeholder="点击选择时间"
                        @click="showPicker = true"
                ></van-field>
            </div>
            <van-popup v-model="showPicker" position="bottom">
                <van-datetime-picker
                        type="date"
                        v-model="currentDate"
                        @confirm="confirm"
                        @cancel="showPicker = false"
                ></van-datetime-picker>
            </van-popup>
            <div class="Boder task_type">
                <div class="label">任务状态</div>
                <van-dropdown-menu active-color="#FF6509">
                    <van-dropdown-item label="" v-model="value1" :options="option1" @change="change"/>
                </van-dropdown-menu>
            </div>
            <!-- <van-field v-model="password" clickable readonly name="任务状态" label="任务状态" placeholder="密码" /> -->
        </div>
        <div class="Index">
            <div class="Title">
                <span>【提示】</span>下单前，请先【设置】注册地省份！国外或港澳的KA，选择【不填】！
                未被接的单子无需点击“辅助失败”按钮，会自动退款；若被接单，必须点击“辅助成功”或“辅助失败”按钮，否则订单将在【15分钟】后自动“订单完成”.【辅助失败】需要在5分钟后才能点击失败，倒计时结束前面点击，结束没有点击系统默认成功。
                【未接单5-10分钟自动退款、辅助失败30分钟自动退款】
            </div>
            <div class="AllList">
                <div class="List" v-for="(item,index) in billList" :key="index">
                    <div class="Lsit_hed">订单号：{{item.id}}</div>
                    <div v-if="item.provinceName" style="margin-bottom: 10px;margin-top: -8px">定向省份：{{item.provinceName}}</div>
                    <div class="List_phone">
                        <div>手机号：{{xPhone(item.phone)}}（全部）</div>
                        <div class="List_phone_right">
                            {{item.createTime}}
                            <!--<span></span>-->
                        </div>
                    </div>
                    <div class="List_Price">
                        <div class="List_Price_left">
                            单价：<span>¥{{item.amount}}</span>
                        </div>
                        <div class="List_Price_Center"></div>
                        <div class="List_Price_Right" :class="`List_Price_Right${item.appealStatus}`">
                            {{item.appealStatus==0?item.statusName:item.appealName}}
                        </div>
                    </div>
                    <div class="List_Bottom">
                        <van-field v-model="item.remark" size="12" center clearable label="备注" placeholder="手机号下单">
                            <van-button slot="button" v-if="item.status == '2' || item.status == '10'" size="small" @click="handelSucc(item.id)">确认成功</van-button>
                            <van-button slot="button" v-if="item.status == '2' || item.status == '10'" style="margin-left: 5px;" size="small" @click="handelFail(item.id)">确认失败</van-button>
                            <van-button slot="button" style="margin-left: 5px;" size="small" @click="addRemark(item,index)">添加备注</van-button>
                        </van-field>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Header from "@/Dpage/Components/Header/index";
    import {formatDate} from "@/Dpage/Utill/utill";
    import {queryBillList, updateOrderRemark,orderSuccess,orderFail} from "../../api/billList";
    import mixin from '@/mixin'

    var list = [
        {
            "appealName": "未申诉",
            "amount": 1280,
            "phone": "13388884444",
            "createTime": "2020-02-27 23:31:11",
            "appealStatus": 0,
            "statusName": "正在扫码",
            "id": 153282817915092992,
            "status": 2
        },
        {
            "appealName": "未申诉",
            "amount": 1280,
            "phone": "13388884444",
            "createTime": "2020-02-27 23:31:11",
            "appealStatus": 0,
            "statusName": "正在扫码",
            "remark": "",
            "id": 1532828179150929921,
            "status": 2
        }
    ]
    export default {
        components: {
            Header
        },
        data() {
            return {
                option1: [
                    {text: "全部", value: ''},
                    {text: "正在扫码", value: 2},
                    {text: "用户确认成功", value: 10},
                    {text: "任务成功", value: 3},
                    {text: "任务失败", value: 5},
                    {text: "正在申诉", value: 6},
                    {text: "未接单退款", value: 9},
                    {text: "商家胜诉", value: 99},
                    {text: "地推胜诉", value: 100},

                ],
                value1: '',
                value: "",
                showPicker: false,
                currentDate: new Date(),
                billList: []
            };
        },
        mixins: [mixin],
        mounted() {
            this.value = formatDate(new Date());
            this.queryBillList()
        },
        methods: {
            queryBillList2() {
                this.billList = []
                this.$toast.loading({
                    message: '刷新中...',
                    forbidClick: true
                });
                this.queryBillList()
            },
            async handelSucc(id) {
                await orderSuccess({orderId:id}).then(res => {
                    this.$toast(res.msg)
                    console.log(res)
                    if(res.code == 200){
                         this.queryBillList()
                    }
                })
            },
            async handelFail(id) {
                await orderFail({orderId:id}).then(res => {
                    this.$toast(res.msg)
                     if(res.code == 200){
                         this.queryBillList()
                    }
                })
            },
            async queryBillList() {
                let that=this
                let {code, data} = await queryBillList({dateTime: this.value, status: this.value1})
                if (code == 200) {
                    that.billList=[]
                    data.forEach(function (item) {
                        item.amount=item.amount/100;
                        
                        that.billList.push(item)
                    })

                }
            },
            async addRemark(val, i) {
                let {code} = await updateOrderRemark({orderId: val.id, remark: val.remark})
                if (code == 200) {
                    this.$toast('添加成功')
                } else {
                    this.billList[i].remark = ''
                }
            },
            confirm(time) {
                this.value = formatDate(time);
                this.showPicker = false;
                this.billList = []
                this.queryBillList()
            },
            change() {
                this.billList = []
                this.queryBillList()
            },
            formatDate(time) {
                return new Date(time).toLocaleDateString()
            }
        }
    };
</script>
<style lang="less">
    .Bill {
        // 头部
        .van-picker-column__wrapper {
            .van-ellipsis {
                width: auto !important;
            }
        }
    }
</style>
<style lang="less" scoped>
    .Bill {
        min-height: 100vh;
        overflow: auto;
        // 头部
        //   End
        .HeaderIndex {
            .Boder {
                border-bottom: 1px solid rgba(0, 0, 0, 0.06);
            }


            .task_type {
                display: flex;
                padding: 0rem 0.42667rem;
                align-items: center;

                .label {
                    width: 2.4rem;
                    color: #323233;
                    font-size: 0.37333rem;
                    line-height: 0.64rem;
                }

                .van-dropdown-menu__title {
                    padding: 0;
                }
            }
        }

        .Index {
            min-height: calc(100vh - 141.7px);
            padding: 15px;
            background: #fbf8fb;
            box-sizing: border-box;
            font-size: 12px;

            .Title {
                span {
                    color: #ff6509;
                }
            }
        }
    }

    .AllList {
        margin-top: 12px;
        color: rgba(51, 51, 51, 1);

        .List {
            margin-bottom: 10px;
            box-sizing: border-box;
            padding: 9px;
            padding-top: 15px;
            width: 345px;
            padding-bottom: 0;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.06);
            border-radius: 8px;

            .Lsit_hed {
                font-size: 13px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: rgba(51, 51, 51, 1);
                padding-bottom: 15px;
            }

            .List_phone {
                color: rgba(51, 51, 51, 1);
                display: flex;
                justify-content: space-between;
                padding-bottom: 8px;

                .List_phone_right {
                    span {
                        margin-right: 5px;
                    }
                }
            }

            .List_Price {
                display: flex;
                justify-content: space-between;
                border-bottom: 1px solid #ededed;

                .List_Price_Center {
                    width: 1px;
                    height: 33px;
                    background: rgba(210, 210, 210, 1);
                }

                .List_Price_left,
                .List_Price_Right {
                    width: 49%;
                    height: 33px;
                    line-height: 33px;
                    text-align: center;
                }

                .List_Price_left {
                    width: 49%;
                    text-align: left;

                    > span {
                        font-size: 16px;
                        color: #ff6509;
                    }
                }

                .List_Price_Right {
                    font-size: 14px;
                    font-family: PingFangSC-Medium, PingFang SC;
                    font-weight: 500;
                    color: rgba(255, 101, 9, 1);
                }

                .List_Price_Right0, .List_Price_Right2 {
                    color: #5192FF;
                }

                .List_Price_Right3, .List_Price_Right99 {
                    color: #00C929;
                }

                .List_Price_Right5, .List_Price_Right100 {
                    color: #FF2809;
                }

                .List_Price_Right6 {
                    color: #999999;
                }

                .List_Price_Right9 {
                    color: #FF6509;
                }

                .List_Price_Right1 {
                    color: #FF2809;
                }
            }
        }
    }
</style>