<template>
    <div class="forgetPwd">
        <div @click="$router.go(-1)" class="back">
            <van-icon name="arrow-left" color="#000"/>
        </div>
        <div class="loginVerification-log">忘记密码</div>
        <div class="transferDetail-content">
            <div class="transferDetail-user">
                <input type="number" v-model="phone" placeholder="您的手机号"/>
            </div>
            <div class="forgetPwd-user">
                <div class="forgetPwd-user-label">新密码</div>
                <input :type="passwordBoole?'text':'password'" v-model="password"/>
                <van-icon class="forgetPwd-eye" :name="passwordBoole?'eye-o':'closed-eye'"
                          @click.stop="passwordBoole=!passwordBoole"/>
                <!--<van-icon name="" />-->
            </div>
            <div class="transferDetail-user">
                <input type="number" v-model="code" placeholder="请输入验证码"/>
                <div class="transferDetail-right2">
                    <div class="timeShow2" v-if="!timeShow" @click="vscode">发送验证码</div>
                    <van-count-down v-else :time="time" format="ss" @finish="finish"/>
                </div>
            </div>
            <div class="transferDetail-submit" @click="submitLogin">登录</div>
        </div>
    </div>
</template>
<script>
    import {mapActions} from "vuex";
    import {forgetPassword} from "../../../api/login";

    export default {
        data() {
            return {
                loginActive: true,
                timeShow: false,
                time: 60 * 1000,
                checked: true,
                phone: '',
                password: '',
                passwordBoole: false,
                code: '',
            };
        },
        mounted() {
        },
        methods: {
            ...mapActions('user', ["vCode", 'login2']),
            finish() {
                this.timeShow = false;
            },
            async vscode() {
                this.vCode(this.phone).then(response => {
                    const {code, msg} = response
                    if (code == 200) {
                        this.timeShow = true;
                    } else {
                        this.$toast(msg);
                    }

                }).catch(() => {
                })
            },
            async submitLogin() {
                /*验证方法执行 */
                if (this.password.length < 6) {
                    this.$toast("请输入大于等于6位数的密码");
                    return false;
                }
                if (this.code.length < 4) {
                    this.$toast("请输入验证码");
                    return false;
                }
                let {code, msg} = await forgetPassword({phone: this.phone, code: this.code, password: this.password})
                this.$toast(msg)
                if (code == 200) {
                    this.$router.replace({name: 'login'})
                }
            },
        }
    };
</script>
<style scoped lang="less">
    .forgetPwd {
        min-height: 100vh;
        font-family: PingFangSC-Regular, PingFang SC;

        .back {
            position: absolute;
            left: 31px;
            top: 62px;
            transform: translateX(-50%);
            width: 10px;
            height: 10px;
            color: #fff;
            font-size: 20px;
        }

        .loginVerification-log {
            margin-top: 129px;
            margin-bottom: 12px;
            text-align: center;
            height: 31px;
            font-size: 22px;
            font-weight: 500;
            color: rgba(51, 51, 51, 1);
            line-height: 31px;
        }

        .transferDetail-content {
            margin-top: 22px;
            padding: 0 31px;

            .transferDetail-user {
                width: 313px;
                border-bottom: 1px solid #d8d8d8;
                display: flex;
                justify-content: space-between;
                align-items: center;

                input {
                    width: 313px;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 23px;
                    padding: 23px 2px;
                    border: none;

                    &::-webkit-input-placeholder {
                        /* placeholder颜色  */
                        color: #999999;
                        /* placeholder字体大小  */
                        font-size: 16px;
                        /* placeholder位置  */
                        text-align: left;
                    }
                }

                .transferDetail-right2 {
                    width: 120px;
                    text-align: center;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(255, 101, 9, 1);
                    line-height: 23px;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        left: -15px;
                        top: -4px;
                        width: 1px;
                        height: 27px;
                        background: #e2e2e2;
                    }

                    .timeShow2 {
                        color: #ff914a;
                    }
                }
            }

            .forgetPwd-user {
                width: 313px;
                border-bottom: 1px solid #d8d8d8;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .forgetPwd-user-label {
                    width: 68px;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 23px;
                }

                .forgetPwd-eye {
                    font-size: 16px;
                    height: 23px;
                    width: 23px;
                    line-height: 23px;
                }

                input {
                    width: 170px;
                    height: 23px;
                    font-size: 16px;
                    font-weight: 400;
                    color: #000000;
                    line-height: 23px;
                    padding: 23px 5px;
                    margin-right: 24px;
                    border: none;
                    text-align: right;

                    &::-webkit-input-placeholder {
                        /* placeholder颜色  */
                        color: #999999;
                        /* placeholder字体大小  */
                        font-size: 16px;
                        /* placeholder位置  */
                        text-align: right;
                    }
                }
            }

            .transferDetail-submit {
                width: 280px;
                height: 40px;
                background: rgba(255, 101, 9, 1);
                border-radius: 8px;
                margin: 112px auto 0 auto;
                font-size: 14px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 40px;
                text-align: center;
            }
        }
    }
</style>