<template>
    <div class="Subordinate">
        <Header title="下级列表"/>
        <div class="Header_top">
            <div class="Boder">
                <van-field
                        right-icon="notes-o"
                        readonly
                        clickable
                        name="datetimePicker"
                        :value="value"
                        label="选择日期"
                        placeholder="点击选择时间"
                        @click="showPicker = true"
                />
            </div>
            <van-popup v-model="showPicker" position="bottom">
                <van-datetime-picker
                        type="date"
                        v-model="currentDate"
                        @confirm="confirm"
                        @cancel="showPicker = false"
                />
            </van-popup>
        </div>
        <div class="Index">
            <div class="Index_Center">直接下级总数：{{subordList.length || 0}}</div>
            <div v-if="false" class="NoArry">
                <div class="img"></div>
                <div class="Bottom_font">暂无下级</div>
            </div>
            <div v-else>
                <div class="YesArry">
                    <div class="List" v-for="(item,index) in subordList" :key="index">
                        <div class="list_top">
                            <div class="List_top_l">
                                <div class="List_top_l_l">
                                    <van-image
                                            width="100%"
                                            round
                                            height="100%"
                                            src="https://img.yzcdn.cn/vant/cat.jpeg"
                                    />
                                </div>
                                <div class="List_top_l_r">{{xPhone(item.phone)}}</div>
                            </div>
                            <div class="List_top_r">注册时间：{{xDate(item.createTime)}}</div>
                        </div>
                        <div class="list_bottom">
                            <div class="list_b_l b_l">
                                <div class="b_l_t">{{item.allNum}}</div>
                                <div class="b_l_b">总下单数</div>
                            </div>
                            <div class="bodercenter"></div>
                            <div class="list_b_r b_l">
                                <div class="b_l_t">{{item.successNum}}</div>
                                <div class="b_l_b">总成功数</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {formatDate} from "@/Dpage/Utill/utill";
    import Header from "@/Dpage/Components/Header/index";
    import mixin from "../../mixin";
    import {queryDirectDownMerchantList} from "@/api/mine";

    var subordList = [
        {
            "allNum": 1,
            "successNum": 1,
            "phone": "15260041580",
            "createTime": "2020-02-27 23:23:06"
        },
        {
            "allNum": 1,
            "successNum": 1,
            "phone": "13045658340",
            "createTime": "2020-02-28 15:06:33"
        }
    ]
    export default {
        components: {
            Header
        },

        mixins: [mixin],
        data() {
            return {
                value: "",
                showPicker: false,
                currentDate: new Date(),
                subordList: []
            };
        },
        mounted() {
            this.value = formatDate(new Date());
            this.queryDirectDownMerchantList()
        },
        methods: {
            async queryDirectDownMerchantList() {
                let {code, data} = await queryDirectDownMerchantList({
                dateTime: this.value
            })
                if (code == 200) {
                    this.subordList = data
                }
            },
            confirm(time) {
                this.value = formatDate(time);
                this.showPicker = false;
                this.queryDirectDownMerchantList()
            }
        }
    };
</script>
<style lang="less" scoped>
    .Index {
        min-height: calc(100vh - 90px);
        padding: 15px;
        background: #fbf8fb;
        box-sizing: border-box;
        font-size: 12px;

        .Index_Center {
            text-align: center;
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
            margin-bottom: 10px;
        }

        .NoArry {
            margin-top: 56px;

            .Bottom_font {
                text-align: center;
                margin-top: 16px;
                font-size: 12px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: rgba(153, 153, 153, 1);
            }

            .img {
                border-radius: 50%;
                margin: 0 auto;
                width: 121px;
                height: 121px;
                background: linear-gradient(
                        180deg,
                        rgba(255, 101, 9, 1) 0%,
                        rgba(255, 128, 53, 0.05) 100%
                );

                img {
                    height: 100%;
                    width: 100%;
                }
            }
        }

        .YesArry {
            .List {
                width: 345px;

                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 1px 8px 0px rgba(246, 246, 246, 1);
                border-radius: 8px;
                padding: 0 16px;
                padding-top: 9px;
                padding-bottom: 9px;
                box-sizing: border-box;
                margin: 0 auto 14px auto;

                .list_top {
                    padding-bottom: 9px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    .List_top_l {
                        display: flex;
                        align-items: center;

                        .List_top_l_l {
                            height: 22px;
                            width: 22px;
                        }

                        .List_top_l_r {
                            font-size: 14px;
                            font-family: PingFangSC-Medium, PingFang SC;
                            font-weight: 500;
                            color: rgba(51, 51, 51, 1);
                            margin-left: 10px;
                        }
                    }

                    .List_top_r {
                        font-size: 12px;
                        font-family: PingFangSC-Regular, PingFang SC;
                        font-weight: 400;
                        color: rgba(153, 153, 153, 1);
                    }

                    display: flex;
                }
            }

            .list_bottom {
                padding-top: 9px;
                border-top: 1px solid #ededed;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .bodercenter {
                    width: 1px;
                    height: 32px;
                    background: rgba(210, 210, 210, 1);
                }

                .list_b_l {
                    .b_l_t {
                        color: #ff6509;
                    }
                }

                .list_b_r {
                    .b_l_t {
                        color: #00c929;
                    }
                }

                .b_l {
                    width: 49%;
                    text-align: center;

                    .b_l_t {
                        font-size: 16px;
                        font-family: PingFangSC-Medium, PingFang SC;
                        font-weight: 500;

                        margin-bottom: 8px;
                    }
                }
            }
        }
    }
</style>