<template>
	<div class="opinion-container">
		<Header  title="意见反馈" />
		<div class="feedback-box">
			<div class="feedback-write">
				<div class="feedback-title"><b>意见反馈</b></div>
				<div><textarea class="ban-textarea" v-model="feedbackContent" placeholder="请输入反馈，最少10个字，最多300字" maxlength="300"></textarea></div>
				<div class="limit"><text>0/300</text></div>
			</div>
			<div class="feedback-operate" v-if="!id">
				<div class="feedback-btn" @click="$router.push({path: '/Personal/opinion/list'});">反馈记录</div>
				<div class="submit-btn"  @click="submitFeedback">确定提交</div>
			</div>
		</div>
	</div>
</template>

<script>
import Header from "@/Dpage/Components/Header/index";
import {declareIdea,getOpinion} from "@/api/mine";
export default {
	components: {
        Header
    },
	data() {
		return {
			current: 0,

			commissionPercent: '',
			feedbackContent: '',
			userinfo: '',
			id:0
		};
	},
	created () {
		this.id = this.$route.params.id;
		if(this.id){
			getOpinion({ideaId:this.id}).then( res =>{
				this.feedbackContent = res.data.content;
			})
		}
    },
	async mounted() {
		// let { code, data } = await getUserInfo();
		// if (code == 200) {
		// 	this.userinfo = data;
		// }\
		
	},
	methods: {
		changeFeedback(e) {
			this.current = e.target.value;
		},
		async submitFeedback() {
		    let obj={}
            // obj['userId']= this.userinfo.userInfo.id
            obj['content']= this.feedbackContent;

            let { code, msg } = await declareIdea(obj);
            this.$toast(msg);
			if (code == 200) {
                setTimeout(()=>{
                    this.$router.go(-1)
                },1500)
			}
			
		}
	}
};
</script>

<style lang="less" scoped>
textarea{
	    resize: none;
    background: none;
    color: inherit;
    opacity: 1;
    -webkit-text-fill-color: currentcolor;
    font: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-align: inherit;
    text-indent: inherit;
    text-transform: inherit;
	text-shadow: inherit;
	border: none;
}
.opinion-container {
	min-height: 100vh;
	background: #f9f9f9;
	.feedback-type {
		display: flex;
		box-sizing: border-box;
		padding: .22rem;
		background: #fff;
		font-size: .33rem;

		.title {
			margin-right: 1.1rem;
		}

		.radio-box {
			display: flex;

			.radio-label {
				display: flex;
				margin-right: .55rem;
			}
		}
	}

	.feedback-box {
		.feedback-write {
			margin-top: .33rem;
			display: flex;
			flex-direction: column;
			background: #fff;

			.feedback-title {
				color: #333;
				box-sizing: border-box;
				padding: .33rem;
                border-bottom: 1px solid #ededed;
                font-size: .44rem;
			}

			.apply-commission {
				display: flex;
				justify-content: space-between;
				align-items: center;
				border-bottom: 1px solid #ededed;
				font-size: .44rem;
				color: #333;
				box-sizing: border-box;
				padding: .33rem;

				.commission-input {
					width: 60%;
					text-align: center;
				}
			}

			.ban-textarea {
				width: 100%;
				height: 3.6rem;
				box-sizing: border-box;
				padding: .33rem;
				font-size: .286rem;
			}

			.limit {
				box-sizing: border-box;
				padding: .33rem;
				color: #999;
				text-align: right;
			}
		}

		.feedback-operate {
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			padding: .66rem 1.3rem;
            text-align: center;
			.feedback-btn {
				font-size: .40rem;
				width: 2.9rem;
                background: #ff6509;
                color: #fff;
                height: .8rem;
                line-height: .8rem;
                border-radius: .1rem;
			}

			.submit-btn {
                font-size: .40rem;
                color: #fff;
				width: 2.9rem;
                background: #5192ff;
                height: .8rem;
                line-height: .8rem;
                border-radius: .1rem;
			}
		}
	}
}
</style>
